const options = [
  ...poolData
]

const accountCategoryList = [
  {
    type: 'My Wallet',
    token: 'My Wallet',
    id: "d04155c3-eb41-4c44-b28d-67bb2d515904",
    icon: Wallet
  },
  {
    type: 'Settlement Collateral',
    token: 'Settlement Collateral',
    icon: GuaranteeIcon,
    id: "edebd20e-18a1-4bc8-a869-9b44537d810c"
  },
  {
    type: "Standard Collateral",
    token: "Standard Collateral",
    icon: AvatarIcon,
    id: "763c1484-3fd3-4044-b8b5-3c261947f414"
  }
]

const {fbtc20mint} = contracts;

export default function GeneralTransfer(props) {
  const { approveStatus, onApprove} = props;
  const [gDisStatus,setGDisStatus] = useState(true)
  const [approveBtnStatus,setApproveBtnStatus] = useState(false)

  const currentCPT = useAppSelector(s => s.collateralManage.currentCPT);
  const {allowedAssets, token, position, avatarAssetList, positionInfoValues, WBTCRouter, tokensValues} = currentCPT;
  const {signer} = useSystemParams()
  const {walletTokenBalance} = useHashPowerToken()
  const [tokenList, setTokenList] = useState([])
  const [actionData, setActionData] = useState([
    {
      accountType: 'My Wallet',
      tokenType: token,
      transferVal: 0,
      id: "1fee1a14-b8b3-4b73-b2be-1817a6d228ec",
      tokenSelectDisabled: false,
      list: [...accountCategoryList]
    },
    {
      accountType: 'Standard Collateral',
      tokenType: token,
      transferVal: 0,
      id: "25897dcf-e843-4630-a5e1-309dc10cb1e5",
      tokenSelectDisabled: true,
      list: [...accountCategoryList]
    }
  ])
  const [params,setParams] = useState<any>({
    nowGDays: 0,
    nowCRate: '0%'
  })

  const {toastInfo, toastSuccess} = useToast()
  const transfer = useTransfer();
  const {expanded, handleChange} = usePanel()
  const {active, chainId, library, account} = useWeb3React();
  const posContract = usePosContract(active, signer);


  const computed = useCallback((value, type)=>{
    console.log('type>>>>', type)
      /**
     * 0 null
    * 1 G->A --->>>  GDays   -interestGuaranteed -t
    * 2 G->W --->>>  GDays  CRate   -interestGuaranteed & -totalAssetsValues
    * 3 A->W --->>>  CRate   -totalAssetsValues
    * 4 A->G --->>>  GDays   +interestGuaranteed
    * 5 W->A --->>>  CRate   +totalAssetsValues
    * 6 W->G --->>>  CRate  GDays  +t  +interestGuaranteed
    *
    * */
    let {totalAssetsValues} = positionInfoValues;
    let {interestGuaranteed} = positionInfoValues;
    if (positionInfoValues) {
      if (type === 1) {
        interestGuaranteed = positionInfoValues.interestGuaranteed*1 - value
      }
      if (type === 2) {
        totalAssetsValues = positionInfoValues.totalAssetsValues*1  - value
        interestGuaranteed = positionInfoValues.interestGuaranteed*1  - value
      }
      if (type === 3) {
        totalAssetsValues = positionInfoValues.totalAssetsValues*1  - value
      }
      if (type === 4) {
        interestGuaranteed = positionInfoValues.interestGuaranteed*1  + value
      }
      if (type === 5) {
        totalAssetsValues = positionInfoValues.totalAssetsValues*1 + value*1;
        console.log('value>', value, totalAssetsValues)
      }
      if (type === 6) {
        totalAssetsValues = positionInfoValues.totalAssetsValues*1 + value
        interestGuaranteed = positionInfoValues.interestGuaranteed*1 + value
      }
      let cRate = 0;
      if (totalAssetsValues === 0) {
        cRate = 0
      } else if (formatWBTC(positionInfoValues.shortBalanceValue) === '0'){
        cRate = Infinity
      } else{
        // @ts-ignore
        cRate = ( totalAssetsValues / positionInfoValues.shortBalanceValue * 100)
      }

      // @ts-ignore
      const gDays = Math.floor( (interestGuaranteed - positionInfoValues.totalObligation) / positionInfoValues.estimatedDailyDebt);
      setParams(state=>({
        ...state,
        futureCRate : formatInfinity(cRate).toString().includes('')? formatInfinity(cRate) : `${formatInfinity(cRate)}%`,
        // eslint-disable-next-line no-restricted-globals
        futureGDays: isNaN(gDays) ? 0 : formatInfinity(gDays),
      }))
    }

  },[positionInfoValues])



  const onSubmit = useCallback(async () => {
    if (posContract) {
      const inter = new utils.Interface(fbtc20mint.abi);
      let data = '';
      let isTransfer = false
      if (actionData[0].accountType === 'My Wallet') {
        if (actionData[1].accountType.includes('Settlement')) {
          // @ts-ignore
          data = inter.encodeFunctionData('addGuarantee', [Math.floor(BigNumber.from(10).pow(tokens[actionData[0].tokenType].decimals).toString() * actionData[0].transferVal) || '0', false]);
          isTransfer = false;
        } else if (actionData[1].accountType.includes('Standard')) {
          // @ts-ignore
          const tx = await transfer(account, tokens[actionData[0].tokenType].address[chainId], position, formatSnNumber((BigNumber.from(10).pow(tokens[actionData[0].tokenType].decimals) * actionData[0].transferVal).toFixed(0)));
          isTransfer = true
        }
      }
      if (actionData[0].accountType.includes('Standard')) {
        isTransfer = false;
        if (actionData[1].accountType === 'My Wallet') {
          // @ts-ignore
          data = inter.encodeFunctionData("withdrawAsset", [tokens[actionData[0].tokenType].address[chainId], account, formatSnNumber((BigNumber.from(10).pow(tokens[actionData[0].tokenType].decimals).toString() * actionData[0].transferVal).toFixed() * 1)])

        }
        if (actionData[1].accountType.includes('Settlement')) {
          // @ts-ignore
          data = inter.encodeFunctionData('addGuarantee', [ formatTheValue(actionData[0].transferVal, tokens[actionData[0].tokenType].decimals).toFixed(0)|| '0', true]);
        }
      }
      if (actionData[0].accountType.includes('Settlement')) {
        isTransfer = false;
        if (actionData[1].accountType === 'My Wallet') {
          // @ts-ignore
          data = inter.encodeFunctionData("moveGuarantee", [account, (BigNumber.from(10).pow(tokens.WBTC.decimals) * actionData[0].transferVal).toFixed(0)])
        }
        if (actionData[1].accountType.includes('Standard')) {
          // @ts-ignore
          data = inter.encodeFunctionData("moveGuarantee", [position, (BigNumber.from(10).pow(tokens.WBTC.decimals) * actionData[0].transferVal).toFixed(0)])
        }

      }


      // @ts-ignore
      // const data = inter.encodeFunctionData('addGuarantee', [BigNumber.from(10).pow(tokens.WBTC.decimals).toString() || '0']);
      if (!isTransfer && data) {
        // @ts-ignore
        const txInfo = await posContract.delegateCall(fbtc20mint.address[chainId], data);
        if (txInfo) {
          toastInfo('Submitted ', (
            <>
              <p style={{marginBottom: 24}}>Waiting for confirmation</p>
              <FlexWrap style={{flexDirection: 'row-reverse'}}>
                <AlertButton onClick={() => {
                  window.open(`https://kovan.etherscan.io/tx/${txInfo.hash}`)
                }}>View on Explorer</AlertButton>
              </FlexWrap>
            </>
          ))
          const result = await txInfo.wait();
          if (result) {
            toastSuccess('Success')
            // setRefresh(state=>!state)
            // setDisabled(false)
          }
        }
      }
    }
    } , [posContract, actionData, transfer, account, chainId, position, toastInfo, toastSuccess])

  const onChangeVal = useCallback(({target}, record) => {
    const {value} = target;
    setActionData(state => {
      const data = state.map(item => ({...item, transferVal: value,}));
      if (tokensValues) {
        // console.log()
        const wbtc = value * tokensValues[record.tokenType];

        console.log("wbtc>>>>", wbtc)
        if (data[0].accountType.includes('Settlement')) {
          if (data[1].accountType.includes('Standard')) {
            computed(wbtc, 1)
          }
          if (data[1].accountType === "My Wallet") {
            computed(wbtc, 2)
          }
        }
        if (data[0].accountType.includes('Standard')) {
          if (data[1].accountType === 'My Wallet') {
            computed(wbtc, 3)
          }
          if (data[1].accountType.includes('Settlement')) {
            computed(wbtc, 4)
          }
        }
        if (data[0].accountType === 'My Wallet') {
          if (data[1].accountType.includes('Standard')) {
            computed(wbtc, 5)
          }
          if (data[1].accountType.includes('Settlement')) {
            computed(wbtc, 6)
          }
        }
      }
      return data;
    })

  }, [tokensValues, computed])

  const onChangeAccount = useCallback((val, record, i) => {
    setActionData(state => {
      const data = state.map((item, index) => ({
        ...item,
        accountType: item.id === record.id ? val : item.accountType,
        tokenType: val.includes('Settlement') ? 'WBTC' : item.tokenType,
      }))
      if (i === 0) {
        data[1].list = accountCategoryList.filter(it=> it.type !== data[0].accountType)
        data[0].tokenSelectDisabled = data[0].accountType.includes('Settlement')
      }
      console.log(data)
      return data
    })

  }, [])

  const onChangeToken = useCallback((val, record) => {
    setActionData(state => state.map((item) => ({
      ...item,
      tokenType: val
    })))

  }, [])

  const onMax = useCallback((record)=>{
    setActionData(state => {
      const data = state
      if (tokensValues && positionInfoValues) {
        const maxMoveAmount = formatWBTC(positionInfoValues.totalAssetsValues*1 - positionInfoValues.shortBalanceValue*1.5)

        let value = 0;
        const {collateralValue, interestGuaranteed} = positionInfoValues;
        if (data[0].accountType.includes('Settlement') && positionInfoValues) {
          if (maxMoveAmount > formatWBTC(collateralValue)) {
            value = maxMoveAmount - formatWBTC(collateralValue)
          }
          if (maxMoveAmount > formatWBTC(interestGuaranteed)) {
            value = formatWBTC(interestGuaranteed)
          }
          if (maxMoveAmount < formatWBTC(interestGuaranteed)) {
            value = toFixed(maxMoveAmount)
          }
          if (data[1].accountType.includes('Standard')) {
            computed(value, 1)
          }
          if (data[1].accountType === "My Wallet") {
            computed(value, 2)
          }
        }
        if (data[0].accountType.includes('Standard') && avatarAssetList) {
          const wbtc = formatWBTC(avatarAssetList[record.tokenType] * tokensValues[record.tokenType])
          if (maxMoveAmount > wbtc) {
            value = wbtc * WBTCRouter[record.tokenType];
          }
          if (maxMoveAmount < wbtc) {
            value = maxMoveAmount * WBTCRouter[record.tokenType]
          }
          if (data[1].accountType === 'My Wallet') {
            computed(wbtc * WBTCRouter[record.tokenType], 3)
          }
          if (data[1].accountType.includes('Settlement')) {
            computed(wbtc, 4)
          }
        }
        if (data[0].accountType === 'My Wallet') {
          value = walletTokenBalance[record.tokenType]
          if (data[1].accountType === 'Avatar') {
            computed(value, 5)
          }
          if (data[1].accountType.includes('Settlement')) {
            computed(value, 6)
          }
        }

        data[0].transferVal = value;
        data[1].transferVal = value;
      }
      return data;
    })

  }, [WBTCRouter, avatarAssetList, computed, positionInfoValues, tokensValues, walletTokenBalance])

  useEffect(() => {
    if (active && isSupportedChainId(chainId)) {
      (async () => {
        let _tokenList = [];
        if (allowedAssets) {
          // eslint-disable-next-line no-restricted-syntax
          for (const address of allowedAssets) {
            const erc20 = getContract(null, ERC20ABI, address.toLocaleLowerCase(), library)
            // console.log(erc20)
            if (erc20) {
              _tokenList.push(Promise.all([erc20.symbol(), erc20.decimals(), address,]))
            }
          }
          _tokenList = (await Promise.all(_tokenList)).map(item => ({
            token: item[0],
            decimals: item[1],
            address: item[2].toLocaleLowerCase(),
            icon: tokens[item[0]].icon
          }))
          setTokenList(_tokenList);
          setActionData(state=>{
            return [state[0], {
              ...state[1],
              list: accountCategoryList.filter(it=> it.type !== state[0].accountType)
            }]
          })
        }
      })();
    }
  }, [active, allowedAssets, chainId, library])


  useEffect(()=>{
    if (active && Number(position) && positionInfoValues) {
      setParams({
        nowGDays: Number(positionInfoValues.gDays) === Infinity ? '+' : positionInfoValues.gDays,
        nowCRate: Number(positionInfoValues.cRate) === Infinity ? '+' : `${positionInfoValues.cRate}%`,
      })
      computed(0, 0);
    }
  }, [active, computed, position, positionInfoValues])

  useEffect(()=>{
    if (actionData[0].transferVal<=0) {
      setGDisStatus(true)
    }else {
      setGDisStatus(false)
    }
  }, [actionData])


  return (
    <Wrapper>
      <Title>General Transfer</Title>
      <Accordion id="panel15" expanded={expanded === 'panel15'} onChange={handleChange('panel15')}>
        <AccordionSummary aria-controls="panel1d-content">
          <Typography>Method 1. Freestyle Transfer</Typography>
        </AccordionSummary>
        <AccordionDetails>
          {actionData.map((item, index) => (
            <div key={item.id}>
              <MethodsWrap>
                <div className="balance_info">
                  <span className="label">Balance: </span>
                  <span>
                  {
                    // eslint-disable-next-line no-nested-ternary
                    (avatarAssetList && positionInfoValues &&  item.accountType === 'Avatar') ? avatarAssetList[item.tokenType]  :
                      item.accountType === 'My Wallet'? walletTokenBalance && walletTokenBalance[item.tokenType] :
                        positionInfoValues && formatWBTC(positionInfoValues.guaranteeValues) || 0
                  } {item.tokenType}
                </span> </div>
                <div className="content">
                  <Select options={item.list} onChange={(val) => {
                    onChangeAccount(val, item, index)
                  }} selectValue={item.accountType}/>
                  <Select options={tokenList} onChange={(val) => {
                    onChangeToken(val, item)
                  }} selectValue={item.tokenType} selectDisabled={index === 1? true : item.tokenSelectDisabled}/>
                  <Input value={item.transferVal} type='number' onInput={(e)=>{
                    onChangeVal(e, item)
                  }} disabled={index === 1}/>
                </div>
                {/* eslint-disable-next-line jsx-a11y/click-events-have-key-events,jsx-a11y/no-static-element-interactions */}
                <div hidden={index === 1} className="func" ><span onClick={()=>{
                  onMax(item)
                }}>Max</span></div>
              </MethodsWrap>

              {
                index === 0 ?
                  <div style={{display: 'flex',alignItems:'center',height:30, justifyContent: 'center'}}><ExchangeArrow onClick={()=>{
                    setActionData(state =>{
                      const list1 = state[0];
                      const list2 = state[1];
                      const data = state.reverse();
                      return data.map((it,i)=>({...it,
                        list: i === 0 ? list1.list : accountCategoryList.filter(ele=> ele.type !== list2.accountType),
                        tokenSelectDisabled: i === 0 ? list1.tokenSelectDisabled : list2.tokenSelectDisabled
                      }))
                    })
                  }} style={{fill: 'none',transform: 'rotate(90deg)', cursor: 'pointer', width: 22}}/></div>
                  : null
              }
            </div>
          ))}
          <DataWrapper
            data={[
              ["Collateral Ratio", params.nowCRate.includes('NaN')? '0%': params.nowCRate , params.futureCRate],
              ["Settlement Collateral Coverage ", `${params.nowGDays || 0} days` , `${params.futureGDays} days`],
            ]}
          />
          {
            approveStatus ?
              <Approve style={{boxShadow: 'none'}} disabled={gDisStatus} onClick={onSubmit}>Submit</Approve>:
              <Approve style={{boxShadow: 'none'}} disabled={approveBtnStatus} onClick={()=>{
                onApprove((state)=>{
                  setApproveBtnStatus(state)
                })
              }
              }>Approve</Approve>
          }</AccordionDetails>
      </Accordion>
     {/* <Accordion id="panel16" expanded={expanded === 'panel16'} onChange={handleChange('panel16')}>
        <AccordionSummary aria-controls="panel1d-content">
          <Typography>Method 2. Rocket Science</Typography>
        </AccordionSummary>
        <AccordionDetails>
          <TextContent>
            Read the docs and programming
          </TextContent>
          <ButtonWrapper style={{boxShadow: 'none', margin: 0}}>Go</ButtonWrapper>
        </AccordionDetails>
      </Accordion> */}
    </Wrapper>
  );
}
