import React, {useEffect, useState} from 'react';
import {styled} from "@mui/material/styles";
// eslint-disable-next-line import/named
import {PoolProps} from 'config/constants/types';
import Select from "../Select";
import {ExchangeArrow, RoundArrow} from "../uikit";


const SelectWrap = styled('div')`
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: static !important;
`

const GroupWrap = styled('div')`
  display: flex;
  flex-direction: column;
  width: calc(100% / 2 - 25px);

  p {
    height: 28px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 15px;
    box-sizing: border-box;

    span {
      font-size: 12px;
      line-height: 17px;
      color: rgba(0, 0, 0, 0.5);
    }
  }

  .maxBtn {
    height: 21px;
    align-items: flex-end;
  }
`

export const IconWrap = styled('strong')`
  display: block;
  width: 20px;
  height: 20px;
  background: #C4C4C4;
  line-height: 20px;
  text-align: center;
  border-radius: 50%
`

export interface MethodsProps {
  options: PoolProps[] // selectIcon
  selectValue: number // current selected
  onMax?: (arg1: PoolProps) => void // to max action
  selectedBalance: number // selected balance
  selectedSymbol: string // selected symbol
  SelectIcon: React.FC // leftTop icon
  RightIcon: React.FC // right icon
  leftInputValue?: number | null // left input value
  rightInputValue?: number // right input value
  rightBalance: number // right balance
  rightSymbol: string // right symbol
  rightOptions: PoolProps[] // options list
  rightSelectedValue: number // right currency value
  selectInput?: (arg1: React.SyntheticEvent) => void,
  leftBalance?: any
}

export default function Index(props) {
  const {
    options,
    leftBalance,
    isExchange,
    onExchange,
    onMax,
    selectInput,
    rightSelectInput,
    SelectIcon,
    RightIcon,
    leftInputValue,
    rightInputValue,
    rightBalance,
    rightDisableStatus,
    leftChange,
    rightChange,
    leftDisableStatus = [false, false],
    leftSelectValue,rightSelectValue,
    rightOptions,
    leftSymbol,rightSymbol,rightNumberIsShow = true
  } = props;
  // rightDisableStatus[1] true selectDisabled
  // rightDisableStatus[2] true inputDisabled

  const [currentSelected, setVal] = useState(options && options.length ? options[0] : {});
  const [rightList, setRightList] = useState(options && options.length ? options[0].rightOptionList : [])
  const [rightVal, setRightVal] = useState(rightList ? rightList[0] : {});


  return (
    <>
      <SelectWrap>
        <GroupWrap>
          <p><SelectIcon/><span className="balance">{leftBalance || currentSelected.balance || 0} {leftSymbol || currentSelected.token || 'WBTC'}</span></p>
          <Select key={1} selectValue={leftSelectValue||currentSelected.token}
                  allDisabled={!leftDisableStatus || leftDisableStatus[0] && leftDisableStatus[1]}
                  inputDisabled={!leftDisableStatus || leftDisableStatus[1]}
                  selectDisabled={!leftDisableStatus || leftDisableStatus[0]}
                  onChange={(val) => {
            const selected = options.filter(it => it.token === val)[0];
            setRightList(selected.rightOptionList)
            setRightVal(selected.rightOptionList[0])
            setVal(selected)
            console.log('selected>>>>', selected)
            // eslint-disable-next-line no-unused-expressions
            leftChange && leftChange(selected)
          }} selectInput={selectInput} actionValue={leftInputValue} options={options} groupStyle={{width: '100%'}}
                  isGroup />
          <p dir='rtl' className='maxBtn'>
            {/* eslint-disable-next-line jsx-a11y/click-events-have-key-events,jsx-a11y/no-static-element-interactions,no-unused-expressions */}
            <span onClick={() => {
              // eslint-disable-next-line no-unused-expressions
              onMax && onMax(currentSelected);
            }}
                  style={{cursor: 'pointer', color: '#6ED6F3', fontSize: '0.75rem', userSelect: "none"}}
            >Max</span>
          </p>
        </GroupWrap>
        {
          isExchange ?
            <ExchangeArrow onClick={onExchange} style={{fill: 'none', cursor: 'pointer', width: 22, marginTop: 8}}/>
            :
            <RoundArrow style={{fill: 'none', width: 22, marginTop: 8}}/>
        }

        <GroupWrap>
          <p>
            <RightIcon/>
            {
              rightNumberIsShow?
                <span className="balance">{rightBalance ||( rightVal&& rightVal.balance || 0)} {rightSymbol || (rightVal&& Object.keys(rightVal) ? rightVal.token : 'WBTC')}</span>
              :null
            }</p>
          <Select key={2} selectValue={rightSelectValue||( rightVal ? rightVal.token : 'WBTC')}
                  options={rightOptions||rightList}
                  actionValue={rightInputValue}
                  groupStyle={{width: '100%'}}
                  isGroup
                  allDisabled={!rightDisableStatus || rightDisableStatus[0] && rightDisableStatus[1]}
                  inputDisabled={!rightDisableStatus || rightDisableStatus[1]}
                  selectDisabled={!rightDisableStatus || rightDisableStatus[0]}
                  selectInput={rightSelectInput}
                  onChange={(val) => {
                    if (currentSelected) {
                      const selected = currentSelected.rightOptionList.filter(it => it.token === val)[0];
                      console.log('options>>>>>>', options, val)
                      // eslint-disable-next-line no-unused-expressions
                      rightChange && rightChange(selected);
                      setRightVal(selected);
                    }else{
                      const selected = options[0].filter(it => it.token === val)[0];
                      console.log('options>>>>>>', options, val)
                      // eslint-disable-next-line no-unused-expressions
                      rightChange && rightChange(selected);
                      setRightVal(selected);
                    }
                  }}
          />
          <p dir='rtl' className='maxBtn'/>
        </GroupWrap>
      </SelectWrap>
    </>
  );
}
