const BootstrapTooltip = styled(({ className, ...props }: TooltipProps) => (
  <Tooltip {...props} arrow classes={{ popper: className }} />
))(({ theme }) => ({
  [`& .${tooltipClasses.arrow}`]: {
    color: theme.palette.common.black,
  },
  [`& .${tooltipClasses.tooltip}`]: {
    backgroundColor: theme.palette.common.black,
  },
}));
const {fbtc20mint} = contracts;

function Mint(props) {
  const {hashPowerTokenList, computed, setRefresh } = useHashPowerToken();
  const {helperContract, signer, setRefreshParams} = useSystemParams()
  const currentCpt = useAppSelector(state=> state.collateralManage.currentCPT)
  const {positionInfoValues, avatarAssetList, position} = currentCpt;
  // const managerList = useManagerList();
  const {active, chainId ,account} = useWeb3React()
  const [selectedCpt,setCpt] = useState(null)
  // @ts-ignore
  const {hidden} = props;
  const { toastSuccess, toastInfo, toastError} = useToast()
  const [rangeVal, setRangeVal] = useState(0);
  const [val, setVal] = useState(100);
  const [mintAmount,setMintAmount] = useState(0)
  const [,setMintAllAmount] = useState(0)
  const [forecastGuaranteeDays,setForecastGuaranteeDays] = useState(0)
  const [dilayReference,setDilayReference] = useState(0)
  const [mintStatus,setMintStatus] = useState(false)
  const [mintActionStatus, setMintActionStatus] = useState(true)

  const {setExpanded} = usePanel();

  const onMax = ()=>{
    if (positionInfoValues) {
      const { totalAssetsValues } = positionInfoValues;

      const maxMintAmountC = totalAssetsValues / (Constants.minMintCRate / 100) / currentCpt.tokensValues[currentCpt.token] - formatTheValue(currentCpt.balance, currentCpt.decimals)

      if (maxMintAmountC > 0 ) {
        computed(maxMintAmountC, currentCpt, (ratio, days, amount )=>{
          const _ratio = (ratio - 100) / 5;
          setRangeVal(_ratio)
          setVal(days)
          setMintAmount(toFixed(maxMintAmountC, '6'));
          /**
           * Mint opening requirements
           * 1.computedd Guaranteedays >= mint Guaranteedays
           * 2.computedd ratio >= mint ratio
           * 3.mint amount > 0
           * */
          console.log('>>>',days>= 80 && Number(ratio) >= 180 && maxMintAmountC>0)
          if (days>= 80 && Number(ratio) >= 180 && maxMintAmountC>0) {
            setMintActionStatus(false)
          }else {
            setMintActionStatus(true)
          }
        })
      }

    }

  }



  const onRange = useCallback((event, v) => {
    setRangeVal(v)
    setVal(v * 5 + 100)
  }, [])
  const onInput = useCallback(({target}) => {
    const {value} = target;
    setRangeVal((value - 100) / 5)
    setVal(value)
  }, [])
  const mintN = useCallback(async ()=>{
    if (active && currentCpt && Number(currentCpt.avatar)) {
      const pos = getContract(signer, PositionABI, currentCpt.position);
      const inter = new utils.Interface(fbtc20mint.abi);
      // @ts-ignore
      const mintData = inter.encodeFunctionData("mint", [account, mintAmount * BigNumber.from(10).pow(currentCpt.decimals)])

      if (helperContract) {
        // @ts-ignore
        // const isHealth = await helperContract.healthCheck(currentCpt.position, mintAmount * BigNumber.from(10).pow(currentCpt.decimals), Number(currentCpt.positionInfoValues.cRate).toFixed(0), Number(currentCpt.positionInfoValues.gDays).toFixed(0));
        // console.log("isHealth>>>", isHealth);
        // @ts-ignore
        const isCollateralFit = Number(val).toFixed(0) >= Constants.minCollateralRatio;
        // @ts-ignore
        const isGuaranteedFit = Number(forecastGuaranteeDays).toFixed(0) >= Constants.minGuaranteeDays;

     /*   if (!isCollateralFit && !isGuaranteedFit) {
          toastWarning('Warning', 'Collateral Ratio and Guarantee Days are both below the minimum')
          return ;
        }
        if (!isCollateralFit) {
          toastWarning('Warning', 'Collateral Ratio below the minimum')
          return ;
        }
        if (!isGuaranteedFit) {
          toastWarning('Warning', 'Guarantee Days below the minimum')
          return ;
        }
        if(mintAmount<=0) {
          toastWarning('Warning', 'The number of Mint is less than zero')
          return ;
        } */
     try {
       if (pos) {
         const info = await pos.delegateCall(fbtc20mint.address[chainId], mintData);
         if (info) {
           setMintAmount(0)
           setMintActionStatus(true)
           toastInfo('Submitted ', (
             <>
               <p style={{marginBottom: 24}}>Waiting for confirmation</p>
               <FlexWrap style={{flexDirection: 'row-reverse'}}>
                 <AlertButton onClick={()=>{window.open(`https://kovan.etherscan.io/tx/${info.hash}`)}}>View on Explorer</AlertButton>
               </FlexWrap>
             </>
           ))
           setMintStatus(true)
           const result = await info.wait();
           if (result) {
             toastSuccess('Success');
             setMintActionStatus(false)
             setRefresh(state=> !state)
             setRefreshParams(state=>!state)
           }
           setMintStatus(false)
         }
       }
     } catch (e) {
       toastError('Fail', e.message)
     }
      }

    }
  },[active, currentCpt, signer, account, mintAmount, helperContract, val, forecastGuaranteeDays, chainId, toastInfo, toastSuccess, setRefresh, toastError, setRefreshParams])

  const {changeCPT} = useUpdataCurrentCPT()
  const onChangeMintAmount = useCallback(({target})=>{
    const {value} = target;
    setMintAmount(value);

    if (value> 0){
      setMintActionStatus(false)
    }else {
      setMintActionStatus(true)
    }
    computed(value, currentCpt, (ratio, gDays, _AllAmount)=>{
      setForecastGuaranteeDays(gDays)
      setRangeVal((ratio - 100) / 5);
      setVal(ratio)
      setMintAllAmount(_AllAmount);
      setDilayReference(formatWBTC((formatTheValue(_AllAmount, currentCpt.decimals)) * currentCpt.rewards / 10))
      if (gDays>= 80 && Number(ratio) >= 180 && value>0) {
        setMintActionStatus(false)
      }else {
        setMintActionStatus(true)
      }
    });
  }, [computed, currentCpt])


  /*
  * Updata Context
  * */
  useEffect(()=>{
    if (active && hashPowerTokenList) {
      setCpt(hashPowerTokenList[0].token);
      if (hashPowerTokenList[0].position && !Number(hashPowerTokenList[0].position)) {
        setMintStatus(true)
      }else{
        setMintStatus(false)
      }

      if (hashPowerTokenList[0].positionInfoValues && hashPowerTokenList[0].positionInfoValues.cRate) {
        computed(mintAmount, currentCpt, (ratio, gDays, _AllAmount)=>{
          console.log('formatTheValue(_AllAmount, currentCpt.decimals)>>>>>>>>', formatTheValue(_AllAmount, currentCpt.decimals), currentCpt.rewards)
          setForecastGuaranteeDays(gDays)
          setRangeVal((ratio - 100) / 5);
          console.log('', ratio)
          setVal(ratio)
          setMintAllAmount(_AllAmount)
          setDilayReference(formatWBTC(_AllAmount * (currentCpt.rewards / 10)))
        });
      }

      // if (Number(currentCpt.avatar)) {
      //   setIsOpen(false)
      // }

    }
  }, [active, chainId, changeCPT, hashPowerTokenList, currentCpt, account, computed, mintAmount])


  return (
    <div hidden={hidden}>
      <CardWrap>
        <Grid container>
          <GridItem item xs={12}>
            <ListStyleOne className="listStyle"/>
            <Title>
              <span>Choose an Asset to Mint</span></Title>
            <SelectGroup>
              <Select actionValue={mintAmount} inputDisabled={mintStatus} selectValue={selectedCpt} groupStyle={{width: '100%'}}  selectInput={onChangeMintAmount} options={hashPowerTokenList || []} isGroup/>
              <Func>
                <ButtonBase onClick={onMax}>Max</ButtonBase></Func>
            </SelectGroup>
            {
              position && Number(position) === 0 ? <Tip to="/collateral"><IconWrap style={{background: "#FF7D00", color: '#fff'}}>!</IconWrap> Please enable the collateral of {selectedCpt} and add collateral first &nbsp;<ArrowLine/></Tip> : null
            }
            {
              currentCpt && Number(currentCpt.position) !== 0 && positionInfoValues&& !(positionInfoValues.gDays * 1) ? <Tip to="/collateral/manage" onClick={()=>{
                setExpanded('panel1')
              }}><IconWrap style={{background: "#FF7D00", color: '#fff'}}>!</IconWrap> Please add collateral first &nbsp; <ArrowLine/></Tip> : null
            }
          </GridItem>
          {
            currentCpt &&  Number(currentCpt.position) === 0 || (Number(currentCpt.position) !== 0 && positionInfoValues && !(positionInfoValues.gDays * 1))?
              null:
            <GridItem item xs={12}>
              <ListStyleTwo className="listStyle"/>
              <Title>
                <div>
                  <span>Set a Mint Collateral Ratio</span>
                  <SubTitle>Set a collateral above the Min. ratio and the Min. Mint Settlement Collateral Coverage</SubTitle>
                </div>
              </Title>
              {/* progress start */}
              <SetMintWrap>
                <Range disabled onChange={onRange} value={rangeVal}/>
                {/* @ts-ignore */}
                <BootstrapTooltip title={`${val === 'Infinity' ? '+' :val}${val === 'Infinity' ? '' :'%'}`} placement="bottom">
                  <div className='input'>
                    {/* @ts-ignore */}
                    <Input disabled type="text" onInput={onInput}  value={val === 'Infinity'?'+': val}
                    />
                    {
                      /* @ts-ignore */
                      val === 'Infinity'?'':<span className='unit disabled'>%</span>
                    }

                  </div>
                </BootstrapTooltip>
              </SetMintWrap>
              {/* progress end */}
              <DataWrapper
                data={[
                  // eslint-disable-next-line eqeqeq,no-nested-ternary
                  ['Settlement Collateral Coverage',`${currentCpt && Number(currentCpt.avatar) ? (currentCpt.positionInfoValues.gDays == "Infinity"? '+': currentCpt.positionInfoValues.gDays) : 0} days`,<span style={{
                    // eslint-disable-next-line no-nested-ternary
                    color: forecastGuaranteeDays >= 400 ? 'rgb(29, 202, 218)' : forecastGuaranteeDays > 200 ? 'rgb(255, 204, 4)': 'rgb(255, 99, 8)'
                  }}>{(String(forecastGuaranteeDays) === 'Infinity' ? '+': forecastGuaranteeDays ) || 0} days</span>  ],
                  ['Daily Paid Rewards Reference' ,`${(positionInfoValues ? formatWBTC(positionInfoValues.estimatedDailyDebt) : 0) || 0 } WBTC`, `${dilayReference} WBTC`  ],
                  // @ts-ignore
                  [<span style={{fontWeight: "500"}}>Min. Mint Settlement Collateral Coverage</span> ,``, <span style={{fontWeight: "500"}}>{`${Constants.minMintGDays} days`}</span>, true  ],
                ]}
              />
            {/*  <MintInfoWrap container>
                <Grid item xs={6}>
                  <h4>Settlement Collateral Coverage</h4>
                  <p>
                    <span>{111} days</span>
                    &gt;
                    <span style={{
                    // eslint-disable-next-line no-nested-ternary
                    color: forecastGuaranteeDays >= 400 ? 'rgb(29, 202, 218)' : forecastGuaranteeDays > 200 ? 'rgb(255, 204, 4)': 'rgb(255, 99, 8)'
                  }}>{(String(forecastGuaranteeDays) === 'Infinity' ? '+': forecastGuaranteeDays ) || 0} days</span>
                  </p>
                   <p>Min. Liq days: {Constants.minGuaranteeDays} days</p>
                  <p>Min. Mint Settlement Collateral Coverage: {Constants.minMintGDays} days</p>
                </Grid>
                <Grid item xs={6}>
                  <h4>Mark Price</h4>
                   @ts-ignore
                  <p>1 F(BTC, 20)  {currentCpt && Number(currentCpt.avatar)? formatWBTC(BigNumber.from(currentCpt.tokensValues[currentCpt.token])) : 0} WBTC</p>
                </Grid>
              </MintInfoWrap> */}
              {/* @ts-ignore */}
              <TipInfo>
                <svg width="16" height="16" viewBox="0 0 16 16" fill="none" xmlns="http://www.w3.org/2000/svg">
                  <path d="M8 16C12.4183 16 16 12.4183 16 8C16 3.58172 12.4183 0 8 0C3.58172 0 0 3.58172 0 8C0 12.4183 3.58172 16 8 16Z" fill="#73797C"/>
                  <path fillRule="evenodd" clipRule="evenodd" d="M7.42295 5.6736C7.17749 5.6736 6.9785 5.47461 6.9785 5.22915V4.0736C6.9785 3.82814 7.17749 3.62915 7.42295 3.62915H8.5785C8.82396 3.62915 9.02295 3.82814 9.02295 4.0736L9.02295 5.22915C9.02295 5.47461 8.82396 5.6736 8.5785 5.6736H7.42295ZM7.42295 12.2012C7.17749 12.2012 6.9785 12.0022 6.9785 11.7567V7.081C6.9785 6.83554 7.17749 6.63656 7.42295 6.63656H8.5785C8.82396 6.63656 9.02295 6.83554 9.02295 7.081V11.7567C9.02295 12.0022 8.82396 12.2012 8.5785 12.2012H7.42295Z" fill="white"/>
                </svg>
                <p>A 1.0% fee of the token value will be levied after the position is neutralized.</p>
              </TipInfo>
            </GridItem>
          }

        </Grid>
        {
          currentCpt &&  Number(currentCpt.position) === 0 || (Number(currentCpt.position) !== 0 &&  positionInfoValues && !(currentCpt.positionInfoValues.gDays * 1))? null :
            <MintButton disabled={mintActionStatus} onClick={mintN}>Mint</MintButton>
        }
        {/* <MintButton disabled={mintStatus} onClick={onMint}>Mint</MintButton> */}

      </CardWrap>
    </div>
  );
}

export default Mint;
