import React, {useCallback, useEffect, useState} from 'react';
import {useWeb3React} from "web3-react-core";
import {BigNumber} from "@ethersproject/bignumber";
import {getContract, getSigner, isSupportedChainId} from "utils/web3React";
import {SupportedChainId} from "config/constants/chains";
import contracts from "config/constants/contracts";
import tokens from "config/constants/tokens";
import {tokensSymbol} from "config/constants/types";
import {useGetTokenBalance} from "hooks/useManager";
import oracleHub from "../utils/oracleHub";


const SystemParamsContext = React.createContext(null)

const {blockUpdater, manager, matrix, helper, pair} = contracts

function SystemParamsProvider({children}) {
  const {library, account, chainId, active} = useWeb3React()
  const [signer, setSigner] = useState(null);
  const [refreshParams, setRefreshParams] = useState(false)
  const [accountContract, setAccountContract] = useState(null)
  const [matrixContract, setMatrixContract] = useState(null)
  const [helperContract, setHelperContract] = useState(null)
  const [params, setParams] = useState({})
  const getTokenBalance = useGetTokenBalance(library, active, chainId)
  // eslint-disable-next-line consistent-return
  const estimateDailyReward = useCallback(async (balance) => {
    if (signer) {
      const updater = getContract(signer, blockUpdater.abi, blockUpdater.address[chainId]);
      if (updater) {
        return updater.estimatedDailyReward(balance)
      }
    }
  }, [chainId, signer])
  const getHashpowerParams = useCallback(async () => {
    if (account) {
      const dailyObligation = await estimateDailyReward(BigNumber.from(10).mul(BigNumber.from(10).pow(tokens[tokensSymbol.FBTC20].decimals)))
      setParams({
        dailyObligation
      });
    }

  }, [account, estimateDailyReward]);
  const [pairWBTCAndUSDT, setPairWBTCAndUSDT] = useState<any>({})
  const [pairWBTCAndFBTC, setPairWBTCAndFBTC] = useState<any>({})
  // eslint-disable-next-line consistent-return
  const getWBTC = useCallback((data) => {
    if (data.symbol === 'F(BTC,20)') {
      const {output} = oracleHub({
        inputBalance: pairWBTCAndFBTC[tokensSymbol.FBTC20],
        outputBalance: pairWBTCAndFBTC.WBTC,
        inputAmount: data.amount,
        inputDecimals: data.decimals,
        outputDecimals: tokens.WBTC.decimals
      })
      return output;
    }
    if (data.symbol === 'USDT') {
      return oracleHub({
        inputBalance: pairWBTCAndUSDT.USDT,
        outputBalance: pairWBTCAndUSDT.WBTC,
        inputAmount: data.amount,
        inputDecimals: data.decimals,
        outputDecimals: tokens.WBTC.decimals
      }).output  * 0.9
    }
    return data.amount
  }, [pairWBTCAndFBTC, pairWBTCAndUSDT])

  // eslint-disable-next-line consistent-return
  const getPairBalance = useCallback(async () => {
    if (getTokenBalance) {
      const data = []
      // eslint-disable-next-line guard-for-in,no-restricted-syntax
      for (const pairKey in pair) {
        // eslint-disable-next-line no-await-in-loop
        const pairBalance = await getTokenBalance(pair[pairKey][chainId])
        data.push(pairBalance)
      }
      setPairWBTCAndFBTC(data[0])
      setPairWBTCAndUSDT(data[1])
      return data;
    }
  }, [getTokenBalance, chainId])


  useEffect(() => {
    let timer;
    if (isSupportedChainId(chainId)) {
      getHashpowerParams();
      getPairBalance()
      timer = setInterval(()=>{
        getPairBalance()
      }, 5000)
    }
    return ()=>{
      clearInterval(timer)
    }
  }, [chainId, getHashpowerParams, getPairBalance, refreshParams])


  useEffect(() => {
    console.log('Generate system parameters')
    if (active && account && chainId && chainId === SupportedChainId.KOVAN) {

      console.log('Generate system parameters true')
      const Signer = getSigner(library, account);
      const accContract = getContract(Signer, manager.accountRegister.abi, manager.accountRegister.address[chainId])
      const _matrixContract = getContract(Signer, matrix.abi, matrix.address[chainId])
      const _helper = getContract(Signer, helper.abi, helper.address[chainId])
      setSigner(Signer);
      setAccountContract(accContract)
      setMatrixContract(_matrixContract)
      setHelperContract(_helper)
    }
    // getPosition();
  }, [account, active, chainId, library])


  return (
    <SystemParamsContext.Provider value={{
      ...params,
      setRefreshParams,
      accountContract,
      matrixContract,
      helperContract,
      signer,
      getWBTC,
      pairWBTCAndUSDT,
      pairWBTCAndFBTC,
      setPairWBTCAndUSDT,
      setPairWBTCAndFBTC,
      getPairBalance
    }}>{children}</SystemParamsContext.Provider>
  );
}

export {SystemParamsProvider, SystemParamsContext}
