import React from 'react';
import {Link } from 'react-router-dom'
import Card from "components/Card";
import {BigNumber} from "@ethersproject/bignumber";
import Table, {StyledTableCell} from "components/Table"
import {Stack} from "@mui/material";
import {useUpdataCurrentCPT} from "state/hooks";
import {TokensIcon} from "config/constants/types";
import useHashPowerToken from "hooks/useHashPowerToken";
import {formatWBTC, formatInfinity} from "utils/formatTheValue";
import Title from "./Title"
import HistoryIcon from "./icon/History";
import Action from "./Action";

const columns =  [
  {
    id: 'ticker',
    numeric: false,
    disablePadding: true,
    isSort: true,
    label: 'Ticker',
  },
  {
    id: 'markPrice',
    numeric: false,
    disablePadding: true,
    isSort: true,
    label: 'Mark Price',
  },
  {
    id: 'collateralMarkValue',
    numeric: true,
    disablePadding: false,
    isSort: true,
    label: 'Collateral Value',
  },
  {
    id: 'collateralRatio',
    numeric: true,
    disablePadding: false,
    isSort: true,
    label: 'Collateral Ratio',
  },
  {
    id: 'guaranteeDays',
    numeric: true,
    disablePadding: false,
    isSort: true,
    label: 'Settlement Collateral Coverage',
  },
  {
    id: 'OutstandingDebtPosition',
    numeric: true,
    disablePadding: false,
    isSort: true,
    label: 'Outstanding Debt Position',
  },
  // {
  //   id: 'dailyObligation',
  //   numeric: true,
  //   disablePadding: false,
  //   isSort: true,
  //   label: 'Daily  Obligation',
  // },
  {
    id: 'action',
    numeric: false,
    disablePadding: false,
    isSort: false,
    label: 'Action',
  },
];

function OutstandingDebt({rewardsData}) {
  const {hashPowerTokenList} = useHashPowerToken()
  const {changeCPT} = useUpdataCurrentCPT()
  // @ts-ignore
  return (
    <Card style={{marginBottom: 20}}>
      <Title>
        <h3>Outstanding Debt</h3>
        <Link to="/history/obligation" ><HistoryIcon />Rewards Paid History</Link>
      </Title>
      <Table
        columns={columns}
        dataSource={hashPowerTokenList}
        pagination={false}
        isMyPage
        content={(row,index, labelId)=>(
          <>
            <StyledTableCell style={{paddingLeft: 0}} component="th" id={labelId} scope="row"><img src={TokensIcon[row.ticker]} style={{display:"block", marginRight: 7,float:'left'}} alt='icon' />{row.ticker}</StyledTableCell>
            <StyledTableCell>{ row.tokensValues? formatWBTC(row.tokensValues[row.token]): 0} WBTC</StyledTableCell>
            <StyledTableCell>{ Number(row.position)? formatWBTC(row.positionInfoValues.totalAssetsValues): 0} WBTC</StyledTableCell>
            {/*  @ts-ignore */}
            {/* eslint-disable-next-line no-nested-ternary */}
            <StyledTableCell>{ Number(row.position) && row.positionInfoValues && (formatInfinity(row.positionInfoValues.cRate).toString('')?`${formatInfinity(row.positionInfoValues.cRate)}%`: `${row.positionInfoValues.cRate}%`) }</StyledTableCell>
            {/* eslint-disable-next-line no-nested-ternary */}
            <StyledTableCell>{ Number(row.position) && row.positionInfoValues  && formatInfinity(row.positionInfoValues.gDays) } days</StyledTableCell>
            {/*  @ts-ignore */}
            <StyledTableCell>{ Number(row.position)? row.balance / BigNumber.from(10).pow(row.decimals) : 0 }</StyledTableCell>
            {/* <StyledTableCell>{ Number(row.position) && row.positionInfoValues ? ` ${formatWBTC(row.positionInfoValues.totalObligation)}` : 0 } WBTC</StyledTableCell> */}
            <StyledTableCell>
              <Stack direction="row" spacing={2}>
                <Link style={{color:"#2AA1FF", fontSize: '14px',
                  fontFamily: 'PingFangSC-Regular,sans-serif'}} to="/collateral/manage" onClick={()=>{
                  changeCPT(row)
                }} >Manage Collateral</Link>
                <Link style={{color:"#2AA1FF", fontSize: '14px',
                  fontFamily: 'PingFangSC-Regular,sans-serif'}} to="/mint/1" onClick={()=>{
                  changeCPT(row)
                }} >Neutralize</Link>
              </Stack>
            </StyledTableCell>
          </>
        )}
      />
    </Card>
  );
}

export default OutstandingDebt;
