import {Approve, DataWrap, icon, IconWrap, TextIcon, TipWrap, Title} from 'components/util'
import {DragDropContext, Draggable, Droppable} from "react-beautiful-dnd";
import uuid from "uuid/dist/v4";
import {useWeb3React} from "web3-react-core";
import {HashPowerTokenProvider} from "contexts";
import Select from "components/Select";
import useHashPowerToken from "hooks/useHashPowerToken";
import {useAppSelector} from "state";
import {useUpdataCurrentCPT} from "state/hooks";
