import oracleHub from "../../utils/oracleHub";

// Avatar->Avatar 0 Guarantee-Avatar->1
enum colType {
  a,
  g
}

const AddBtn = styled(Button)`
  box-shadow: none !important;
  width: 34px !important;
  height: 28px !important;
  padding: 0 !important;
  background: linear-gradient(90deg, #62C3F7 0%, #7AE9F1 100%) !important;
  border-radius: 6px !important;

  svg {
    color: #000;
    width: 16px;
  }

`

const Wrapper = styled('div')`
  padding-right: 44px;
  width: 100%;
  overflow: scroll;

  .left_func {
    overflow: scroll;
    height: calc(100vh - 200px);
    padding-bottom: 100px !important;
  }
`

const Content = styled('div')`
  min-width: 1024px;

`
const RightCard = styled(Card)`

`


const {liquidation, helper} = contracts
const onDragEnd = (result, columns, setColumns) => {
  if (!result.destination) return;
  const {source, destination} = result;
  if (source.droppableId !== destination.droppableId) {
    const sourceColumn = columns[source.droppableId];
    const destColumn = columns[destination.droppableId];
    const sourceItems = [...sourceColumn.items];
    const destItems = [...destColumn.items];
    const [removed] = sourceItems.splice(source.index, 1);
    destItems.splice(destination.index, 0, removed);
    setColumns([...destColumn, destItems]);
  } else {
    const copiedItems = [...columns];
    const [removed] = copiedItems.splice(source.index, 1);
    copiedItems.splice(destination.index, 0, removed);
    // if (copiedItems.map(it=>it.input>0).includes(false)){
    //   setIsSubmit(true)
    // }else{
    //   setIsSubmit(false)
    // }
    setColumns(copiedItems);
  }
};

function LiquidationDetail() {
  const {active, chainId, account} = useWeb3React()
  const {hashPowerTokenList, setRefresh} = useHashPowerToken();
  const {pairWBTCAndUSDT, pairWBTCAndFBTC, signer, getWBTC, helperContract} = useSystemParams();
  const {changeCPT} = useUpdataCurrentCPT();
  // eslint-disable-next-line react-hooks/exhaustive-deps
  const currentCpt = useAppSelector(state => state.collateralManage.currentCPT) || {}
  const {avatarAssetList, positionInfoValues, token, position, address, balance, decimals, rewards,tokensValues} = currentCpt
  const posContract = usePosContract(active, signer)
  const computedPrice = useComputedPrice()
  const {toastSuccess, toastError, toastWarning} = useToast()
  const [disabled,setDisabled] = useState(true)
  const [columns, setColumns] = useState([])
  const [tokensList, setTokensList] = useState([]);
  const [neutralizeData, setNeutralizeData] = useState([])
  const [addType,setAddType] = useState(0)
  const [transferVal, setTransferVal] = useState({
    m1: 0,
    m2: 0,
    m3: 0,
    m4: 0
  });
  const [params,setParams] = useState<any>({
    nowGDays: 0,
    nowCRate: '0%'
  })
  const [selectedValue, setSelectedValue] = React.useState('2');
  const handleChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    setSelectedValue(event.target.value);
  };

  const computed = useCallback((data)=>{
    if (positionInfoValues) {
      if (data) {
        let collateralValues = 0;
        let guaranteeValues = 0;
        let outstandingDebt = 0;
        data.forEach((item)=>{
          collateralValues += (toFixed(getWBTC({amount: item.output,symbol: item.outputToken.token, decimals: item.outputToken.decimals}), 'WBTC') * 1);
          collateralValues -= (toFixed(getWBTC({amount: item.input, symbol: item.inputToken.token, decimals: item.inputToken.decimals}), 'WBTC') * 1);
          if (item.type === colType.g) {
            guaranteeValues -= item.input
          }
        });

        if (transferVal.m3) {
          guaranteeValues += transferVal.m3;
        }
        if (transferVal.m4 > 0) {
          outstandingDebt -= transferVal.m4;
        }
        // @ts-ignore
        const totalAssetsValues = (positionInfoValues.totalAssetsValues * 1) + (collateralValues=== 0 ? 0: toWBTC(toFixed(collateralValues, 'WBTC')*1));
        // @ts-ignore
        const interestGuaranteed = positionInfoValues.interestGuaranteed - guaranteeValues

        const cRate = ()=>{
          if (Number(totalAssetsValues) === 0){
            return 0
          }
          if (Number(positionInfoValues.shortBalanceValue) === 0) {
            return 0
          }
          return (totalAssetsValues / (tokensValues[token] * (formatTheValue(balance, decimals) + outstandingDebt)) * 100).toFixed(2)
        }
        const gDays = ()=>{
          if ((interestGuaranteed - positionInfoValues.totalObligation) === 0) {
            return 0;
          }
          if (Number(positionInfoValues.estimatedDailyDebt) === 0) {
            return 0
          }
          return Math.floor( (interestGuaranteed - positionInfoValues.totalObligation) / positionInfoValues.estimatedDailyDebt)
        }
         setParams(state=>({
          ...state,
          outstandingDebt: formatTheValue(balance, decimals) + outstandingDebt,
          futureCRate : formatInfinity(cRate()).toString().includes('') ? formatInfinity(cRate()): `${formatInfinity(cRate())}%`,
          futureGDays: gDays(),
          dailyObligationReference: formatWBTC((formatTheValue(balance, decimals) + outstandingDebt) * rewards / 10)
        }))
      }
    }
  },[positionInfoValues, transferVal.m3, transferVal.m4, getWBTC, tokensValues, token, balance, decimals, rewards])
  const onChange = useCallback((val) => {
    if (hashPowerTokenList) {
      const tokenData = hashPowerTokenList.filter(item => item.token === val)[0]
      changeCPT(tokenData)
    }
  }, [hashPowerTokenList, changeCPT])
  const addMethods = useCallback(() => {
    setColumns(state => {
      const data = [
        ...state ]
      if (addType === colType.a) {
        const list = [...tokensList]
        const index = list.findIndex(it=>it.token === 'WBTC');
        const copy = list.splice(index,1)
        list.unshift(...copy)
        const rightIndex = list[0].rightOptionList.findIndex(it=>it.token === token)
        list[0].rightOptionList.unshift(...(list[0].rightOptionList.splice(rightIndex,1)))
        data.push({
          id: uuid(),
          type: colType.a,
          input: 0,
          inputToken: list[0],
          outputToken: list[0].rightOptionList[0],
        })
      }
      if (addType === colType.g) {
        const list = [...tokensList]
        const index = list.findIndex(it=>it.token === 'WBTC');
        const copy = list.splice(index,1)
        list.unshift(...copy)
        const rightIndex = list[0].rightOptionList.findIndex(it=>it.token === 'WBTC');
        list[0].rightOptionList.unshift(...(list[0].rightOptionList.splice(rightIndex,1)))
        data.push({
          id: uuid(),
          type: colType.g,
          input: 0,
          inputToken: list[0],
          outputToken: list[0].rightOptionList[0],
        })
      }
      // if (data.map(it=>it.input>0).includes(false)){
      //   setIsSubmit(true)
      // }else{
      //   setIsSubmit(false)
      // }
      return data
    })
  }, [addType, token, tokensList])
  const onChangeInputAmount = useCallback((e, item) => {
    const value = formatSnNumber(e.target.value);
    console.log("item>>>>>", item)
    setColumns(state => {
      const data = state.map(it => {
        const _item = {
          ...it,
          input: it.id === item.id ? e.target.value : it.input,
          // eslint-disable-next-line no-nested-ternary
          output: it.id === item.id ? (it.inputToken.token === item.outputToken.token ? e.target.value : toFixed(computedPrice(item, e.target.value), item.outputToken.token)) : it.output ,
        }
        // eslint-disable-next-line no-unused-expressions
        // e.target.value<=0 ? _item.output = 0 : ''
        // setP
        // _item.oracleHub = computedPrice()
        return _item;
      })
      //
      // if (data.map(it=>it.input>0 && it.output>0).includes(false)){
      //   setIsSubmit(true)
      // }else if (value.indexOf('.')!== -1 && String(value).slice(value.indexOf('.')+1).length > item.inputToken.decimals ){
      //   setIsSubmit(true)
      // }else{
      //   setIsSubmit(false)
      // }
      computed(data)
      return data;
    });

  }, [computed, computedPrice])
  const onChangeToken = useCallback((selected, id, type) => {
    setColumns(state => {
      return state.map(item => {
        if (item.id === id) {
          const data = {...item, [type]: selected};
          if (data.inputToken.token === selected.token) {
            data.output = data.input
          } else{
            data.output = toFixed(computedPrice(data, data.input), data.outputToken.token)
          }
          if (type === 'inputToken')  {
            data.output = 0;
            data.input = 0;
          }
          // data.oracleHub = computedPrice(data,1);

          return data
        }
        return item
      })
    })
  }, [computedPrice])
  const onMax = useCallback((record, index) => {
    setColumns(state => {
      const data = state.map((item, i) => ({
        ...item,
        // eslint-disable-next-line no-nested-ternary
        input: i === index ? ( record.type=== colType.g && positionInfoValues ?formatWBTC(positionInfoValues.interestGuaranteed) : record.inputToken.allBalance) : item.input,
        // eslint-disable-next-line no-nested-ternary,no-self-compare
        output: item.id === record.id ? (record.type === colType.g && positionInfoValues? formatWBTC(positionInfoValues.interestGuaranteed) :toFixed(computedPrice(item, record.inputToken.allBalance), record.outputToken.token)) : item.output,
      }))
      computed(data);
      return data;
    });
  }, [computed, computedPrice, positionInfoValues])
  const changeTransferVal = useCallback((e, type, key) => {
    if (type > 0) {
      if (key === 'm1') {
        if (positionInfoValues) {
          setTransferVal(state => ({
            ...state,
            [key]: formatWBTC(positionInfoValues.interestGuaranteed)
          }))
        }
      }
      if (key === 'm3') {
        setTransferVal(state => ({
          ...state,
          [key]: avatarAssetList.WBTC
        }))
      }
      if (key === 'm4'|| key=== 'm2') {
        setTransferVal(state => ({
          ...state,
          [key]: avatarAssetList[token]
        }))
      }

      // computed(walletTokenBalance.WBTC.toFixed(6))
    } else {
      setTransferVal(state => ({
        ...state,
        [key]: e.target.value
      }));
      // computed(e.target.value)
      // if (e.target.value && e.target.value>0) {
      //   setDisabled(false)
      // }else{
      //   setDisabled(true)
      // }
    }
  }, [avatarAssetList, positionInfoValues, token])
  const onSubmit = useCallback(async ()=>{
    const inf = new utils.Interface(liquidation.abi);
    if (posContract) {
      const result = await helperContract.healthCheck(position, balance, 15000, 60)

      if (result.includes(false)) {
        const _params = columns.map(it => {
          // @ts-ignore
          const inputAmount = formatSnNumber(Math.floor(it.input * BigNumber.from(10).pow(it.inputToken.decimals)));
          const m = []
          const path = [];
          if (it.type === colType.a ) {
            if ((it.inputToken.token === 'USDT' || it.outputToken.token === 'USDT') && (it.inputToken.token === 'F(BTC,20)' || it.outputToken.token === 'F(BTC,20)') ){
              path.push(it.inputToken.address[chainId]);
              path.push(tokens.WBTC.address[chainId]);
              path.push(it.outputToken.address[chainId]);
            }else{
              path.push(it.inputToken.address[chainId]);
              path.push(it.outputToken.address[chainId]);
            }
            console.log('path>>>', path);
            const dataCode = inf.encodeFunctionData("swapUniV2_swapExactTokensForTokens",[inputAmount, '0', path, '999999999999999']);
            console.log('swapUniV2_swapExactTokensForTokens>>>>', inf.decodeFunctionData('swapUniV2_swapExactTokensForTokens', dataCode))
            m.push(
              liquidation.address[chainId], dataCode
            )
          }
          if (it.type === colType.g) {
            m.push(
              liquidation.address[chainId],
              inf.encodeFunctionData('moveGuaranteeBack', [inputAmount])
            )
          }
          return m;
        })
        const methods = []


        if (selectedValue === '1' && transferVal.m3) {
          const m3 = inf.encodeFunctionData('addGuarantee', [Math.floor(toWBTC(transferVal.m3))])
          methods.push([liquidation.address[chainId], m3])
        }
        console.log('selectedValue>>>>', selectedValue)
        if (selectedValue === '2' && transferVal.m4) {
          const m4 = inf.encodeFunctionData('neutralizetBalance', [Math.floor(toHashToken(transferVal.m4))])
          console.log('neutralizetBalance', inf.decodeFunctionData('neutralizetBalance', m4))
          methods.push([liquidation.address[chainId], m4])
        }
        const dataMulti = inf.encodeFunctionData("liquidation", [selectedValue,[..._params, ...methods]])

        console.log('decode', inf.decodeFunctionData('liquidation', dataMulti))

        try {
          const info = await posContract.delegateCall(liquidation.address[chainId], dataMulti);
          if (info) {
            const res = await info.wait();
            if (res) {
              setRefresh(state=> !state);
              toastSuccess('Success')
            }
          }
        } catch (e) {
          toastError('Failed', e.message)
        }
      }
      else{
        toastWarning('Warning', 'Liquidation conditions not met')
      }
    }
  }, [posContract, helperContract, position, balance, columns, transferVal.m3, transferVal.m4, selectedValue, chainId, setRefresh, toastSuccess, toastError, toastWarning])
  // change add methods     Avatar->Avatar | Guarantee-> Avatar
  const handleAddType = useCallback((e)=>{
    const {value} = e.target;
    setAddType(Number(value))
  }, [])

  useEffect(() => {
    if (active && avatarAssetList) {
      const list = [];
      // eslint-disable-next-line no-restricted-syntax,guard-for-in
      for (const tokensKey in tokens) {
        const item = tokens[tokensKey]
          const tokenItem = {
            token: tokensKey,
            address: item.address,
            allBalance: formatSnNumber(avatarAssetList[tokensKey]),
            balance: toFixed(avatarAssetList[tokensKey], tokensKey),
            icon: item.icon,
            decimals: item.decimals,
            rightOptionList: []
          };
          // eslint-disable-next-line no-restricted-syntax,guard-for-in
          for (const tokensKeyB in tokens) {

            tokenItem.rightOptionList.push({
                token: tokensKeyB,
                address: tokens[tokensKeyB].address,
                icon: tokens[tokensKeyB].icon,
                allBalance: formatSnNumber(avatarAssetList[tokensKeyB]),
                balance: toFixed(avatarAssetList[tokensKeyB], tokensKeyB),
                decimals: tokens[tokensKeyB].decimals
              })
          }
          list.push(tokenItem);
      }

      setTokensList(list);
      let price = 0
      if (list) {
        price = computedPrice({
          inputToken: list[0],
          outputToken: list[0].rightOptionList[0]
        })
        const index = list.findIndex(it=>it.token === 'WBTC');
        const copy = list.splice(index,1)
        list.unshift(...copy)
        const rightIndex = list[0].rightOptionList.findIndex(it=>it.token === token)
        list[0].rightOptionList.unshift(...(list[0].rightOptionList.splice(rightIndex,1)))

        setColumns(state => {
          return [{
            id: uuid(),
            type: colType.a,
            input: 0,
            output: 0,
            inputToken: list[0],
            outputToken: list[0].rightOptionList[0],
            oracleHub:price
          }]
        })
      }
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [active, avatarAssetList])

  useEffect(() => {
    if (currentCpt && Object.keys(currentCpt).length) {
      const data = [{
        ...currentCpt,
        rightOptionList: [currentCpt]
      }];
      setNeutralizeData(data)

    }
  }, [currentCpt])

  useEffect(()=>{
    computed(columns);
  }, [columns, transferVal.m4, transferVal.m3, computed])

  return (
    <Wrapper>
      <Content>
        <Title>Liquidation</Title>
        <Select selectDisabled isGroup={false} options={hashPowerTokenList || []}
                selectValue={currentCpt ? currentCpt.token : ''}
                onChange={onChange}/>
        <Grid container spacing={2} style={{marginTop: 14}}>
          <Grid className='left_func' style={{paddingTop: 0}} item xs={8}>
            <RightCard>
              {/* trading through Avatar */}
              <DragDropContext
                onDragEnd={(result) => onDragEnd(result, columns, setColumns)}
              >
                <Droppable droppableId="droppableId">
                  {(provided) => {
                    return (
                      <div
                        {...provided.droppableProps}
                        ref={provided.innerRef}
                      >
                        {columns.map((item, index) => {
                          return (
                            <Draggable
                              key={item.id}
                              draggableId={item.id}
                              index={index}
                            >
                              {/* @ts-ignore */}
                              {/* eslint-disable-next-line @typescript-eslint/no-shadow */}
                              {(provided) => {
                                return (
                                  <div
                                    ref={provided.innerRef}
                                    {...provided.draggableProps}
                                    {...provided.dragHandleProps}
                                    style={{
                                      display: "flex",
                                      alignItems: 'center',
                                      userSelect: "none",
                                      margin: "0 0 8px 0",
                                      ...provided.draggableProps.style,
                                      position: 'static'
                                    }}
                                  >
                                    <div style={{width: "100%", position: 'relative',}}>
                                      <Methods
                                        SelectIcon={()=> (<span>{item.type === colType.a ?'Standard Collateral':'Settlement Collateral'}</span>)} leftInputValue={item.input}
                                        rightInputValue={item.output}
                                        options={tokensList}
                                        rightOptions={item.inputToken.rightOptionList}
                                        leftSelectValue={item.inputToken.token}
                                        rightSelectValue={item.outputToken.token}
                                        leftBalance={item.type=== colType.g && positionInfoValues? formatWBTC(positionInfoValues.interestGuaranteed):  (item.inputToken.balance || item.inputToken.allBalance)}
                                        rightBalance={item.outputToken.balance || item.outputToken.allBalance}
                                        selectedBalance={100}
                                        leftDisableStatus={[item.type === colType.g, false]}
                                        RightIcon={()=>(<span>Standard Collateral</span>)}
                                        rightDisableStatus={[item.type === colType.g, true]}
                                        selectInput={(e) => {
                                          onChangeInputAmount(e, item)
                                        }}
                                        leftChange={(selected) => {
                                          onChangeToken(selected, item.id, 'inputToken')
                                        }}
                                        rightChange={(selected) => {
                                          onChangeToken(selected, item.id, 'outputToken')
                                        }}
                                        onMax={() => {
                                          onMax(item, index);
                                        }}
                                        leftSymbol={item.inputToken.token}
                                        rightSymbol={item.outputToken.token}
                                      />
                                      <p style={{
                                        position: 'absolute',
                                        bottom: 0,
                                        right: 0,
                                        color: 'rgba(0, 0, 0, 0.5)',
                                        fontSize: 12
                                      }}>
                                        1{item.inputToken.token}  {item.inputToken.token === item.outputToken.token? 1: computedPrice(item, 1)}{item.outputToken.token}
                                      </p>
                                    </div>
                                    <IconButton onClick={() => {
                                      const data = [...columns];
                                      data.splice(index, 1);
                                      // if (data.map(it=>it.input>0).includes(false)){
                                      //   setIsSubmit(true)
                                      // }else{
                                      //   setIsSubmit(false)
                                      // }
                                      setColumns(data);
                                    }}
                                                style={{
                                                  cursor: 'pointer',
                                                  marginTop: 8,
                                                  marginLeft: 10,
                                                  padding: 0
                                                }}><DeleteIcon/></IconButton>
                                    <IconButton style={{marginTop: 8, marginLeft: 8, padding: 0}}><DroppableIcon
                                      style={{fill: 'none'}}/></IconButton>
                                  </div>
                                );
                              }}
                            </Draggable>
                          );
                        })}{provided.placeholder}

                      </div>
                    );
                  }}
                </Droppable>
              </DragDropContext>
              <div style={{display: "flex",
                alignItems: "center", marginTop: 10, marginBottom: 40}}>
                <AddBtn onClick={addMethods}><AddIcon/></AddBtn>
                <FormControl style={{marginLeft: 10}}>
                  <RadioGroup
                    row
                    aria-labelledby="demo-row-radio-buttons-group-label"
                    name="row-radio-buttons-group"
                    value={addType} onChange={handleAddType}
                  >
                    <FormControlLabel value={0} control={<Radio />} label={(
                      <p style={{display: "flex",
                        alignItems: "center",
                        justifyContent: "space-between"}}><span>Standard Collateral</span><span style={{margin:'0 10px'}}>-&gt;</span><span>Standard Collateral</span></p>
                    )} />
                    <FormControlLabel value={1} control={<Radio />} label={
                      <p style={{display: "flex",
                        alignItems: "center",
                        justifyContent: "space-between"}}><span>Settlement Collateral</span><span style={{margin:'0 10px'}}>-&gt;</span><span>Standard Collateral</span></p>
                    } />
                  </RadioGroup>
                </FormControl>
              </div>
              {/* add guarantee */}
              <div style={{display:"flex", alignItems: "center",paddingRight: 28, width: '100%'}}>
                <div style={{width: "100%"}}>
                  <Methods
                    SelectIcon={()=>(<span>Standard Collateral</span>)} options={poolData}
                    rightBalance={positionInfoValues ? formatWBTC(positionInfoValues.guaranteeValues) : 0}
                    rightOptions={poolData}
                    leftBalance={avatarAssetList ? avatarAssetList.WBTC : 0}
                    RightIcon={()=>(<span>Settlement Collateral</span>)}
                    leftInputValue={transferVal.m3}
                    rightInputValue={transferVal.m3}
                    selectInput={(e) => {
                      changeTransferVal(e, 0, 'm3')
                    }}
                    onMax={() => {
                      changeTransferVal({}, 1, 'm3')
                    }}
                  />
                </div>
                <Radio
                  checked={selectedValue === '1'}
                  onChange={handleChange}
                  value="1"
                  name="radio-buttons"
                  style={{
                    cursor: 'pointer',
                    marginTop: 8,
                    marginLeft: 10,
                    padding: 0
                  }}
                  inputProps={{ 'aria-label': '1' }}
                />
              </div>
              {/* neutralize */}
              <div style={{display:"flex", alignItems: "center",paddingRight: 28, width: '100%'}}>
                <div style={{width: '100%'}}>
                  <Methods
                    SelectIcon={()=>(<span>Standard Collateral</span>)} options={neutralizeData} rightOptions={neutralizeData}
                    leftBalance={avatarAssetList ? avatarAssetList[token] : 0}
                    RightIcon={icon(TextIcon, [<span style={{color: "#f40"}}>Neutralize</span>])}
                    leftSelectValue={token}
                    leftSymbol={token}
                    rightSelectValue={token}
                    leftInputValue={transferVal.m4}
                    rightInputValue={transferVal.m4}
                    rightNumberIsShow={false}
                    selectInput={(e) => {
                      changeTransferVal(e, 0, 'm4')
                    }}
                    onMax={() => {
                      changeTransferVal({}, 1, 'm4')
                    }}
                  />
                </div>
                <Radio
                  checked={selectedValue === '2'}
                  onChange={handleChange}
                  value="2"
                  style={{
                  cursor: 'pointer',
                  marginTop: 8,
                  marginLeft: 10,
                  padding: 0
                }}
                  name="radio-buttons"
                  inputProps={{ 'aria-label': '2' }}
                />
              </div>

              <DataWrapper
                data={[
                  ["OutStanding Debt" , formatTheValue(balance, decimals) , params.outstandingDebt],
                  ["Daily Obligation Reference", positionInfoValues? formatWBTC(positionInfoValues.estimatedDailyDebt) : 0, params.dailyObligationReference],
                  ["Collateral Ratio",positionInfoValues && formatInfinity(positionInfoValues.cRate).toString().includes('')?
                    formatInfinity(positionInfoValues.cRate) : `${formatInfinity(positionInfoValues.cRate)}%` || 0,
                    params.futureCRate
                  ],
                  ["Settlement Collateral Coverage", `${positionInfoValues ?
                    formatInfinity(positionInfoValues.gDays) : 0} days`,`${ params.futureGDays} days`]
                ]}
              />
              <TipWrap>
                <IconWrap style={{background: "#FF7D00", color: '#fff'}}>!</IconWrap>
                <p>The collateral ratio should lower than 200% and the Settlement Collateral Coverage should lower than 100 days after the liquidation.</p>
              </TipWrap>
              <Approve onClick={onSubmit}>Submit</Approve>
            </RightCard>
          </Grid>
          <Grid item style={{paddingTop: 0, marginBottom: 40, paddingBottom: 50}}  xs={4}>
            <MyCollateral pageType="Liq"/>
          </Grid>
        </Grid>
      </Content>
    </Wrapper>
  );
}

export default LiquidationDetail;
