const { faucet } = contracts

// Inspired by blueprintjs
export function BpCheckbox(props: CheckboxProps) {
  return (
    <CheckboxWrap
      sx={{
        '&:hover': {bgcolor: 'transparent'},
      }}
      disableRipple
      color="default"
      checkedIcon={<BpCheckedIcon/>}
      icon={<BpIcon/>}
      inputProps={{'aria-label': 'Checkbox demo'}}
      {...props}
    />
  )
}

export default function Faucet() {
  const {account, connector, chainId, active, library} = useWeb3React()
  const {signer} = useSystemParams()
  const [faucetContract,setFacuetContract] = useState(null)
  const [limitDay,setLimitDays] = useState(null)
  const [isAirdrop,setIsAirdrop] = useState(true)
  const [airDropToken,setAirDropToken] = useState({WBTC: true, USDT: true})
  const {toastSuccess, toastError, toastInfo} = useToast()

  const addToMetaMask = useCallback((symbol)=>{
    if (active && chainId === SupportedChainId.KOVAN) {
      addAsset({connector, tokenSymbol:symbol , chainId})
    }
  },[active, chainId, connector])


  useEffect(()=>{
    let timer;
    if (signer) {
      console.log(process.env.REACT_APP_reCAPTCHA_keys)
      timer = setTimeout(() => {
        // @ts-ignore
        window.grecaptcha.enterprise.render('html_element', {
          sitekey: process.env.REACT_APP_reCAPTCHA_keys,
          hl:'en',
          callback(res) {
            if (res) {
              setIsAirdrop(false)
            }
          },
        })
      }, 2000)
      const contract = getContract(signer, faucet.abi, faucet.address[chainId]);
      if (contract) {
        const _limitDay = [contract.limitDay(tokens.WBTC.address[chainId]), contract.limitDay(tokens.USDT.address[chainId])]
        Promise.all(_limitDay).then(data=>{
          setLimitDays({
            WBTC: data[0],
            USDT: data[1]
          })
        })
      }
      setFacuetContract(contract)
    }
    return ()=>{
      clearTimeout(timer)
    }
  },[chainId, signer])

  const handleAirdrop = useCallback(async  ()=>{
    if (faucetContract) {
      const info = [];
      setIsAirdrop(true)
      if (airDropToken.WBTC) {
        info.push(faucetContract.airdrop(tokens.WBTC.address[chainId]))
      }
      if (airDropToken.USDT) {
        info.push(faucetContract.airdrop(tokens.USDT.address[chainId]))
      }
     try {
       console.log('faucet go go go')
       const [wbtcTx, usdtTx] = await Promise.all(info);
       if (wbtcTx) {
         toastInfo('Submitted');
         const result = await wbtcTx.wait()
         if (result) {
           toastSuccess('Success');
         }
       }
       if (usdtTx){
         toastInfo('Submitted');
         const result = await usdtTx.wait()
         if (result) {
           toastSuccess('Success');
         }
       }
     } catch (e) {
       setIsAirdrop(false)
       toastError('Fail', e.message)
     }
    }
  }, [airDropToken, chainId, faucetContract, toastError, toastInfo, toastSuccess])


  return (
    <>
      <Title>Faucet</Title>
      <SubTitle>You can claim the test tokens from the faucet once every 24 hours.</SubTitle>
      <div style={{width: '100%',overflow:'scroll'}}>
        <Wrapper>
          <Grid container>
            <Grid item xs={8}>
              <CardWrap>
                <h3>Wallet address</h3>
                <Input value={account} disabled type="text"/>
                <h3>Request tokens</h3>
                <Grid container spacing={4}>
                  <Grid item xs={6}>
                    <TokenWrap>
                      <FormControlLabel control={<BpCheckbox checked={airDropToken.WBTC} onChange={(e,val)=>{
                        setAirDropToken(state=>({...state, WBTC: val}))
                      }}/>} label="test WBTC" labelPlacement="end"/>
                      <Input type="number" disabled value={limitDay? formatWBTC(limitDay.WBTC) : 0} dir="rtl"/>
                    </TokenWrap>
                  </Grid>
                  <Grid item xs={6}>
                    <TokenWrap>
                      <FormControlLabel control={<BpCheckbox checked={airDropToken.USDT} onChange={(e,val)=>{
                        setAirDropToken(state=>({...state, USDT: val}))
                      }}/>} label="test USDT" labelPlacement="end"/>
                      <Input type="number" disabled value={limitDay? limitDay.USDT.toString() / 1e+18 : 0} dir="rtl"/>
                    </TokenWrap>
                  </Grid>
                </Grid>
                <Grid container spacing={4} className="addToWallet">
                  <Grid item xs={6}>
                    <ButtonText className="btn" onClick={()=>{
                      addToMetaMask('WBTC')
                    }}>add to MetaMask</ButtonText>
                  </Grid>
                  <Grid item xs={6}>
                    <ButtonText className="btn" onClick={()=>{

                      addToMetaMask('USDT')
                    }}>add to MetaMask</ButtonText>
                  </Grid>
                </Grid>
                <div id="html_element"/>
                <ButtonWrap disabled={isAirdrop} onClick={handleAirdrop}>Send Request</ButtonWrap>
                {/* eslint-disable-next-line jsx-a11y/anchor-is-valid */}
                <p className="link"><a target="_blank" href='https://faucetlink.to/kovan' className="link" rel="noreferrer">Request Ethereum Kovan testnet ETH from a faucet</a>
                </p>
              </CardWrap>
            </Grid>
          </Grid>
        </Wrapper>
      </div>
    </>

  )
}
