const {fbtc20mint} = contracts
function NeutralizeComp(props) {
  const {hashPowerTokenList, setRefresh, walletTokenBalance, computed} = useHashPowerToken()
  const {signer} = useSystemParams()
  const currentCpt = useAppSelector(state => state.collateralManage.currentCPT);
  const {active, chainId, account} = useWeb3React()
  const [approveStatus, setApproveStatus] = useState(false)
  const [dataSource, setDataSource] = useState([])
  const [disabledAction, setDisabledAction] = useState(true);
  const {position, balance, decimals, token, avatarAssetList, address, positionInfoValues, tokensValues, rewards} = currentCpt;
  const [neutralizeAmount, setNeutralizeAmount] = useState(0)
  const posContract = usePosContract(active, signer)
  const [params, setParams] = useState<any>()
  const {toastSuccess, toastError, toastInfo} = useToast()
  const [neutralizeType, setNeutralizeType] = useState('wallet')

  const {handleApprove} = useApprove(account, address)
  const {onCheckApprove} = useCheckApprove()

  const onApprove = useCallback(async () => {
    if (active && chainId && Number(position)) {
      console.log('111')
      try {
        const info = await handleApprove(position);
        setApproveStatus(true)
        if (info.hash) {
          const result = await info.wait();
          if (result) {
            toastSuccess('Approve Success')
          }
        }
      } catch ({message}) {
        setApproveStatus(false)
        toastError('Failed', `${message}`)
      }

    }
  }, [active, chainId, position, handleApprove, toastSuccess, toastError])

  const checkApprove = useCallback(async () => {
    if (active && chainId && Number(position)) {
      const txInfo = await onCheckApprove(account, tokens[token].address[chainId], position)
      if (txInfo && txInfo._hex > 0) {
        setApproveStatus(true)
      } else {
        setApproveStatus(false)
      }
    }

  }, [account, active, chainId, onCheckApprove, position, token])

  const computedParams = useCallback(() => {
    if (currentCpt) {
      computed(neutralizeAmount - 2 * neutralizeAmount, currentCpt, (ratio, gDays, mintAllAmount) => {
        setParams({
          gDays,
          cRate: ratio,
          outstandingDebt: mintAllAmount,
          estimateNeutralizeFee: formatWBTC(tokensValues[token] * neutralizeAmount * 0.01),
          dailyObligationReference: rewards ? formatWBTC(rewards / 10) * mintAllAmount : 0
        })
        console.log(ratio, gDays, mintAllAmount)
      })
    }

  }, [computed, currentCpt, neutralizeAmount, rewards, token, tokensValues])
  const onChangeNeutralizeAmount = useCallback(({target}) => {
    const {value} = target;
    setNeutralizeAmount(value);
    if (value > 0) {
      setDisabledAction(false)
    } else {
      setDisabledAction(true)
    }
    if (neutralizeType === 'wallet' && walletTokenBalance && value > walletTokenBalance[token]) {
      setDisabledAction(true)
    }
    if (neutralizeType === 'avatar' && avatarAssetList && value > avatarAssetList[token] ) {
      setDisabledAction(true)
    }
  }, [avatarAssetList, neutralizeType, token, walletTokenBalance])


  const onNeutralize = useCallback(async () => {
    if (active && Number(position)) {
      const interf = new utils.Interface(fbtc20mint.abi);
      // @ts-ignore
      const data = interf.encodeFunctionData('neutralizetBalance', [(BigNumber.from(10).pow(decimals) * neutralizeAmount).toFixed(0) || '0', neutralizeType !== 'avatar'])
      if (posContract) {
        try {
          // @ts-ignore
          const info = await posContract.delegateCall(fbtc20mint.address[chainId], data);

          if (info) {
            setDisabledAction(true);
            setNeutralizeAmount(0)
            toastInfo('Submitted')
            const result = await info.wait();
            if (result) {
              setRefresh(state => !state)
              toastSuccess('Success')
              setDisabledAction(false);
            }

          }
        } catch (e) {
          toastError('Fail', e.message)
        }

      }

    }
  }, [active, chainId, decimals, neutralizeAmount, posContract, position, setRefresh, toastError, toastInfo, toastSuccess, neutralizeType])

  useEffect(() => {
    if (active && Number(position) && hashPowerTokenList) {
      setDataSource(hashPowerTokenList.map(it => ({...it, icon: ''})))
    }
  }, [active, hashPowerTokenList, position])


  useEffect(() => {
    checkApprove();
  }, [checkApprove, currentCpt, position])

  useEffect(() => {
    computedParams()
  }, [computedParams, neutralizeAmount])


  return (
    <>
      <SelectGroup>
        <Select selectValue={token} groupStyle={{width: '100%'}} options={dataSource} actionValue={neutralizeAmount}
                selectInput={onChangeNeutralizeAmount} isGroup/>
        <Func dir="rtl">
          <ButtonBase onClick={() => {
            const dec = BigNumber.from(10).pow(decimals)
            if (neutralizeType === 'avatar') {

              if (avatarAssetList && avatarAssetList[token]) {
                // @ts-ignore
                if ((avatarAssetList[token] * dec) > Number(balance)) {
                  // @ts-ignore
                  setNeutralizeAmount(balance / dec)
                } else {
                  setNeutralizeAmount(avatarAssetList[token])
                }
                if (avatarAssetList[token] > 0) {
                  setDisabledAction(false)
                } else {
                  setNeutralizeAmount(0)
                  setDisabledAction(true)
                }
              }else {
                setNeutralizeAmount(0)
                setDisabledAction(true)
              }
            }
            if (neutralizeType === 'wallet') {
              if (walletTokenBalance && walletTokenBalance[token]) {
                // @ts-ignore
                if ((walletTokenBalance[token] * dec) > Number(balance)) {
                  // @ts-ignore
                  setNeutralizeAmount(balance / dec)
                } else {
                  setNeutralizeAmount(walletTokenBalance[token])
                }
                if (walletTokenBalance[token] > 0) {
                  setDisabledAction(false)
                } else {
                  setNeutralizeAmount(0)
                  setDisabledAction(true)
                }
              }else {
                setNeutralizeAmount(0)
                setDisabledAction(true)
              }
            }


          }}>Max</ButtonBase>
        </Func>
        <SelectTypeWrap>
          <h3>Neutralize the asset from</h3>
          <div className="content">
            {/* eslint-disable-next-line jsx-a11y/click-events-have-key-events,jsx-a11y/no-static-element-interactions */}
            <div className={`item_wrap ${neutralizeType === 'wallet' ? 'active' : ''}`} onClick={()=>{
              setNeutralizeType("wallet")
            }}>
              <div className="item wallet">
                <h4><Wallet /> My Wallet</h4>
                <p><span>{token}</span><span style={{
                  color:(neutralizeType === 'wallet'  && walletTokenBalance && neutralizeAmount > walletTokenBalance[token]) ? 'red': ''
                }}>{walletTokenBalance && walletTokenBalance[token] || 0}</span></p>
                <p><span>WBTC</span><span style={{
                  color:(neutralizeType === 'wallet' && walletTokenBalance && params && params.estimateNeutralizeFee > walletTokenBalance.WBTC ) ? 'red': ''
                }}>{walletTokenBalance && walletTokenBalance.WBTC || 0}</span></p>
              </div>
            </div>
            {/* eslint-disable-next-line jsx-a11y/click-events-have-key-events,jsx-a11y/no-static-element-interactions */}
            <div className={`item_wrap ${neutralizeType === 'avatar' ? 'active' : ''}`}  onClick={()=>{
              setNeutralizeType("avatar")
            }}>
              <div className="item avatar">
                <h4><AvatarIcon />Standard Collateral</h4>
                <p><span>{token}</span><span style={{
                  color:(neutralizeType === 'avatar' && avatarAssetList && neutralizeAmount > avatarAssetList[token]) ? 'red': ''
                }}>{avatarAssetList && avatarAssetList[token] || 0}</span></p>
                <p><span>WBTC</span><span style={{
                  color:(neutralizeType === 'avatar' && avatarAssetList && params && params.estimateNeutralizeFee > avatarAssetList.WBTC ) ? 'red': ''
                }}>{avatarAssetList && avatarAssetList.WBTC || 0}</span></p>
              </div>
            </div>
          </div>
        </SelectTypeWrap>
      </SelectGroup>
      <Tip>
        <table className="info_panel" cellSpacing={20} cellPadding={20}>
          <tr>
            <td className="from" colSpan={2}>From</td>
            <td className="to">To</td>
          </tr>
          <tr>
            <td className="label">Outstanding Debt</td>
            <td className="from">{balance ? formatTheValue(balance, decimals) : 0}</td>
            <td className="to">{balance ? formatTheValue(balance, decimals) - neutralizeAmount : 0}</td>
          </tr>
       {/*   <tr>
            <td className="label">Daily Obligation Reference</td>
            <td className="from">{positionInfoValues && rewards ? formatWBTC(rewards/ 10 * formatTheValue(balance, decimals)) : 0} WBTC</td>
            <td className="to"> {params ? params.dailyObligationReference : 0}WBTC</td>
          </tr>
         */} <tr>
            <td className="label">Collateral Ratio</td>
            {/* eslint-disable-next-line no-restricted-globals */}
            <td className="from">{positionInfoValues && (formatInfinity(positionInfoValues.cRate).toString().includes('') ? formatInfinity(positionInfoValues.cRate) : `${isNaN(positionInfoValues.cRate) ? '0' : formatInfinity(positionInfoValues.cRate)}%`)}</td>

            {/* eslint-disable-next-line no-restricted-globals */}
            <td className="to">{params && (formatInfinity(params.cRate).toString().includes('') ? formatInfinity(params.cRate) : `${isNaN(params.cRate) ? '0' : formatInfinity(params.cRate)}%`)}</td>
          </tr>
          <tr>
            <td className="label">Settlement Collateral Coverage</td>
            {/* eslint-disable-next-line no-restricted-globals */}
            <td className="from">{positionInfoValues && (formatInfinity(positionInfoValues.gDays).toString().includes('') ? formatInfinity(positionInfoValues.gDays) : `${isNaN(positionInfoValues.gDays) ? '0' : formatInfinity(positionInfoValues.gDays)}`)} days</td>

            {/* eslint-disable-next-line no-restricted-globals */}
            <td className="to">{params && (formatInfinity(params.gDays).toString().includes('') ? formatInfinity(params.gDays) : `${isNaN(params.gDays) ? '0' : formatInfinity(params.gDays)}`)} days</td>
          </tr>
          <tr className='footer'>
            <td className="label">Estimate neutralize fee</td>
            <td className="to" colSpan={2}  style={{
              color:(
                (neutralizeType === 'wallet' && walletTokenBalance && params && params.estimateNeutralizeFee > walletTokenBalance.WBTC) ||
                (neutralizeType === 'avatar' && avatarAssetList && params && params.estimateNeutralizeFee > avatarAssetList.WBTC) ) ?
                'red': ''
            }}>{params && `${ params.estimateNeutralizeFee }`|| 0} WBTC</td>
          </tr>
        </table>
      {/*  <br/>
        <br/>
        <br/>
        <br/>
        <br/>
        <br/>
        <br/>
        <br/>
        <p>
          <span>Estimate neutralize fee</span>
          <span>
            <span>{params && params.estimateNeutralizeFee} WBTC</span>
        </span>
        </p>
        <p>
          <span>Outstanding Debt</span>
          <span>
           @ts-ignore
            <span>{balance ? formatTheValue(balance, decimals) : 0}</span> &gt;
            <span>{balance ? formatTheValue(balance, decimals) - neutralizeAmount : 0}</span>
        </span>
        </p>
        <p>
          <span>Daily Obligation Reference</span>
          <span>
           @ts-ignore
            <span>{positionInfoValues && rewards ? formatWBTC(rewards/ 10 * formatTheValue(balance, decimals)) : 0} WBTC</span> &gt;
            <span> {params ? params.dailyObligationReference : 0}WBTC</span>
        </span>
        </p>
        <p>
          <span>Collateral Ratio</span>
           eslint-disable-next-line no-restricted-globals
          <span>{positionInfoValues && (formatInfinity(positionInfoValues.cRate).toString().includes('') ? formatInfinity(positionInfoValues.cRate) : `${isNaN(positionInfoValues.cRate) ? '0' : formatInfinity(positionInfoValues.cRate)}%`)}</span> &gt;
           eslint-disable-next-line no-restricted-globals
          <span>{params && (formatInfinity(params.cRate).toString().includes('') ? formatInfinity(params.cRate) : `${isNaN(params.cRate) ? '0' : formatInfinity(params.cRate)}%`)}</span>
        </p>
        <p>
          <span>Collateral Ratio</span>
           eslint-disable-next-line no-restricted-globals
          <span>{positionInfoValues && (formatInfinity(positionInfoValues.gDays).toString().includes('') ? formatInfinity(positionInfoValues.gDays) : `${isNaN(positionInfoValues.gDays) ? '0' : formatInfinity(positionInfoValues.gDays)}`)}</span> &gt;
           eslint-disable-next-line no-restricted-globals
          <span>{params && (formatInfinity(params.gDays).toString().includes('') ? formatInfinity(params.gDays) : `${isNaN(params.gDays) ? '0' : formatInfinity(params.gDays)}`)}</span>
        </p>
   */}   </Tip>
      <TipInfo>
        {
          (
            (neutralizeType === 'wallet' && walletTokenBalance && params && params.estimateNeutralizeFee > walletTokenBalance.WBTC) ||
            (neutralizeType === 'avatar' && avatarAssetList && params && params.estimateNeutralizeFee > avatarAssetList.WBTC) )? <svg width="16" height="16" viewBox="0 0 16 16" fill="none" xmlns="http://www.w3.org/2000/svg">
            <path d="M8 16C12.4183 16 16 12.4183 16 8C16 3.58172 12.4183 0 8 0C3.58172 0 0 3.58172 0 8C0 12.4183 3.58172 16 8 16Z" fill="#FF2E00"/>
            <path fillRule="evenodd" clipRule="evenodd" d="M8.57852 10.156C8.82398 10.156 9.02296 10.355 9.02296 10.6004V11.756C9.02296 12.0015 8.82398 12.2004 8.57852 12.2004H7.42296C7.1775 12.2004 6.97852 12.0015 6.97852 11.756V10.6004C6.97852 10.355 7.1775 10.156 7.42296 10.156H8.57852ZM8.57852 3.62842C8.82398 3.62842 9.02296 3.8274 9.02296 4.07286V8.74859C9.02296 8.99405 8.82398 9.19303 8.57852 9.19303H7.42296C7.1775 9.19303 6.97852 8.99405 6.97852 8.74859V4.07286C6.97852 3.8274 7.1775 3.62842 7.42296 3.62842H8.57852Z" fill="white"/>
          </svg>: <svg width="16" height="16" viewBox="0 0 16 16" fill="none" xmlns="http://www.w3.org/2000/svg">
            <path d="M8 16C12.4183 16 16 12.4183 16 8C16 3.58172 12.4183 0 8 0C3.58172 0 0 3.58172 0 8C0 12.4183 3.58172 16 8 16Z" fill="#73797C"/>
            <path fillRule="evenodd" clipRule="evenodd" d="M7.42295 5.6736C7.17749 5.6736 6.9785 5.47461 6.9785 5.22915V4.0736C6.9785 3.82814 7.17749 3.62915 7.42295 3.62915H8.5785C8.82396 3.62915 9.02295 3.82814 9.02295 4.0736L9.02295 5.22915C9.02295 5.47461 8.82396 5.6736 8.5785 5.6736H7.42295ZM7.42295 12.2012C7.17749 12.2012 6.9785 12.0022 6.9785 11.7567V7.081C6.9785 6.83554 7.17749 6.63656 7.42295 6.63656H8.5785C8.82396 6.63656 9.02295 6.83554 9.02295 7.081V11.7567C9.02295 12.0022 8.82396 12.2012 8.5785 12.2012H7.42295Z" fill="white"/>
          </svg>
        }



        <p  style={{
          color:(
            (neutralizeType === 'wallet' && walletTokenBalance && params && params.estimateNeutralizeFee > walletTokenBalance.WBTC) ||
            (neutralizeType === 'avatar' && avatarAssetList && params && params.estimateNeutralizeFee > avatarAssetList.WBTC) ) ? 'red': ''
        }}>A 1.0% fee of the token value will be levied after the position is neutralized.</p>
      </TipInfo>
      {
        neutralizeType === 'avatar' ?
          <NeutralizeButton disabled={disabledAction} onClick={onNeutralize}>Neutralize</NeutralizeButton>
          :
          (
            <>{
              approveStatus ?
                <NeutralizeButton disabled={Number(position) ? disabledAction : true}
                                  onClick={onNeutralize}>Neutralize</NeutralizeButton> :
                <NeutralizeButton disabled={Number(position) ? approveStatus : true}
                                  onClick={onApprove}>Approve</NeutralizeButton>
            }</>
          )
      }
    </>
  );
}

export default NeutralizeComp;
