import React, {useCallback, useContext, useEffect, useState} from 'react';
import {useHistory} from "react-router-dom";
import {useWeb3React} from "web3-react-core";
import {Button, Stack, IconButton} from '@mui/material';
import {styled} from '@mui/material/styles'
import useToast from "hooks/useToast";
import {Button as ButtonStyled} from 'components/uikit/components/Button'
import ButtonUnstyled, {buttonUnstyledClasses, ButtonUnstyledProps,} from '@mui/base/ButtonUnstyled';
import contracts from 'config/constants/contracts'
import {useUpdataCurrentCPT} from "state/hooks";
import {ShareIcon} from 'components/uikit/components/Svg'
import {ContractTransaction} from "ethers";
import {FlexWrap} from "components/util";
import { OpenStatus } from 'config/constants/types';
import {getContract, getSigner} from "../../../utils/web3React";
import {ComputedPowerTokenContext} from "../../../contexts/ComputedPowerTokenContext";
import useHashPowerToken from "../../../hooks/useHashPowerToken";
import useSystemParams from "../../../hooks/useSystemParams";
import usePanel from "../../../hooks/usePanel";


const blue = {
  500: '#007FFF',
  600: '#0072E5',
  700: '#0059B2',
  color: '#2AA1FF'
};

const CustomButtonRoot = styled(Button)`
  //display: block;
  font-family: PingFangSC-Regular, sans-serif;
  text-transform: none;
  font-size: 0.875rem;
  border-radius: 8px;
  transition: all 150ms ease;
  cursor: pointer;
  border: none;
  padding: 0 !important;
  color: ${blue.color};
  text-align: left;
  display: block !important;
  &:hover {
    background-color: transparent;
  }

  &.${buttonUnstyledClasses.active} {
    background-color: transparent;
  }

  &.${buttonUnstyledClasses.focusVisible} {
    //box-shadow: 0 4px 20px 0 rgba(61, 71, 82, 0.1), 0 0 0 5px rgba(0, 127, 255, 0.5);
    outline: none;
  }

  &.${buttonUnstyledClasses.disabled} {
    opacity: 0.5;
    cursor: not-allowed;
  }
`;

const AlertButton = styled(ButtonStyled)`
  box-shadow: 0px 2px 0px rgba(0, 0, 0, 0.043) !important;
  border-radius: 14px !important;
  background: linear-gradient(270deg, #7AE9F1 0%, #61C3F7 100%) !important;
  font-size: 14px !important;
  height: 32px !important;
  line-height: 32px !important;
  color: #000 !important;
  font-weight: normal !important;
`

function CustomButton(props: ButtonUnstyledProps) {
  return <ButtonUnstyled {...props} component={CustomButtonRoot}/>;
}



const {manager} = contracts;

function Action({row, refresh}) {
  const {setExpanded} = usePanel();
  const {account, active, library} = useWeb3React()
  const {status, avatar, address, token} = row;
  const [openText, setOpenText] = useState<string>()
  const [txDatas, setTxInfo] = useState<ContractTransaction>()
  const {chainId} = useWeb3React();
  const {accountContract} = useSystemParams()
  const {toastInfo, toastError, toastSuccess} = useToast();
  const {changeCPT} = useUpdataCurrentCPT()
  const history = useHistory();

  const onOpen = useCallback(async () => {
    if (active && account) {
      if (accountContract) {
        try {
          const txInfo = await accountContract.createPosition(address[chainId]);
          setOpenText(txInfo)
          if (txInfo!==undefined && txInfo.hash) {
            toastInfo('Submitted', (
              <>
                <p style={{marginBottom: 24}}>Waiting for confirmation</p>
                <FlexWrap style={{flexDirection: 'row-reverse'}}>
                  <AlertButton onClick={()=>{window.open(`https://kovan.etherscan.io/tx/${txInfo.hash}`)}}>View on Explorer</AlertButton>
                </FlexWrap>
              </>
            ))
            setTxInfo(txInfo)
            setOpenText('Enabling');
            const info = await txInfo.wait();

            if (info.confirmations !== 0) {
              const position = await accountContract.positions(account, address[chainId]);
              toastSuccess('Success', (
                <>
                  <p style={{marginBottom: 24}}>{token} collateral account was opened.
                    You can add collateral first and then go to mint the token.</p>
                  <FlexWrap style={{flexDirection: 'row-reverse'}}>
                    <AlertButton onClick={()=>{window.open(`https://kovan.etherscan.io/address/${position}`)}}>View on Explorer</AlertButton>
                  </FlexWrap>
                </>
              ))
              refresh(state=>!state)
              setOpenText('')
            }
          }
        } catch (error) {
          if(error) {
            toastError('Failed', error.message)
          }
        }
      }

    }
  }, [active, account, chainId, address, toastInfo, toastSuccess, token, refresh, toastError, accountContract])
  const onWaiting = useCallback(async ()=>{
    if (txDatas) {
      const info = await library.getTransaction(txDatas.hash);
      if (info.confirmations === 0) {
        toastInfo('Submitted', (
          <>
            <p style={{marginBottom: 24}}>Waiting for confirmation</p>
            <FlexWrap style={{flexDirection: 'row-reverse'}}>
              <AlertButton onClick={()=>{window.open(`https://kovan.etherscan.io/tx/${txDatas.hash}`)}}>View on Explorer</AlertButton>
            </FlexWrap>
          </>
        ))
      }else{
        refresh(state => !state)
      }

    }
 /*   library.once('block', async (block) => {
      console.log(block);
      toastInfo('Submitted', (
        <>
          <p style={{marginBottom: 24}}>Waiting for confirmation</p>
          <FlexWrap style={{flexDirection: 'row-reverse'}}>
            <AlertButton onClick={()=>{window.open(`https://kovan.etherscan.io/tx/${txDatas.hash}`)}}>View on Explorer</AlertButton>
          </FlexWrap>
        </>
      ))

    });
 */ },[library, refresh, toastInfo, txDatas])



  return (
    <div>
      <Stack direction="row" spacing={2}>
        {
          openText === 'Enabling' ?
            <CustomButtonRoot onClick={onWaiting}>Enabling</CustomButtonRoot>:
            <CustomButtonRoot onClick={onOpen} disabled={status !== OpenStatus.notOpen}>{
              // eslint-disable-next-line no-nested-ternary
              status === OpenStatus.notOpen ? 'Enable' : 'Enabled'
            }</CustomButtonRoot>
        }
        <CustomButtonRoot onClick={() => {
          changeCPT(row);
          setExpanded('panel1')
          history.push('/collateral/manage')
        }} disabled={!Number(row.avatar)}>
          Manage
        </CustomButtonRoot>
        <IconButton onClick={() => {
          window.open(`https://kovan.etherscan.io/address/${avatar}`)
        }} disabled={false} >
          <ShareIcon color={Number(avatar)? blue.color : '#ccc'} />
        </IconButton>
      </Stack>
    </div>
  );
}

export default Action
