import React, {useCallback, useState, useContext, useEffect} from 'react';
import {InputBase} from "@mui/material";
import SearchIcon from '@mui/icons-material/Search';
import {styled} from '@mui/material/styles';
import {ComputedPowerTokenContext} from 'contexts/ComputedPowerTokenContext'
import Card from 'components/Card'
import SwitchButton from "components/Switch";
import {BG_COMMON_COLOR} from 'components/uikit/widgets/Menu/config';
import Table, {StyledTableCell} from 'components/Table'
import {BigNumber} from "@ethersproject/bignumber";
import {TokensIcon} from 'config/constants/types'
import Title from "./components/Title";
import Action from "./components/Action";
import {HashPowerTokenProvider} from "../../contexts";
import useHashPowerToken from "../../hooks/useHashPowerToken";
import useSystemParams from "../../hooks/useSystemParams";


export const Search = styled('div')`
  display: flex;
  align-items: center;
  justify-content: space-between;
  position: relative;
  box-sizing: border-box;
  border-radius: 11px;
  height: 44px;
  margin-bottom: 34px;
  .opened {
    font-family: PingFangSC-regular, sans-serif;
    font-size: 12px;
    line-height: 17px;
  }
`
export const SearchIconWrapper = styled('div')(({theme}) => ({
  padding: theme.spacing(0, 2),
  height: '100%',
  position: 'absolute',
  pointerEvents: 'none',
  display: 'flex',
  alignItems: 'center',
  justifyContent: 'center',
}));
export const StyledInputBase = styled(InputBase)(({theme}) => ({
  color: 'inherit',
  width: '100%',
  height: '44px',
  '& .MuiInputBase-input': {
    padding: theme.spacing(1, 1, 1, 0),
    paddingLeft: `calc(1em + ${theme.spacing(4)})`,
    background: `${BG_COMMON_COLOR}`,
    boxSizing: "border-box",
    borderRadius: "11px",
    border: '1px solid rgba(207, 216, 226, 0.5)',
    transition: theme.transitions.create('width'),
    width: '100%',
    height: '100%',
    caretColor: '#6ED6F3',
    '&::-webkit-search-cancel-button': {
      height: "1em",
      width: "1em",
      borderRadius: '50em',
      background: 'url(https://pro.fontawesome.com/releases/v5.10.0/svgs/solid/times-circle.svg) no-repeat 50% 50%',
      backgroundSize: 'contain',
      opacity: "0.3",

    }
  }
}));

const headCells = [
  {
    id: 'ticker',
    numeric: false,
    disablePadding: true,
    isSort: true,
    label: 'Ticker',
  },
  /*  {
      id: 'liquidity',
      numeric: true,
      disablePadding: false,
      isSort: true,
      label: 'Liquidity',
    }, */
  {
    id: 'outstandingDebt',
    numeric: true,
    disablePadding: false,
    isSort: true,
    label: 'Outstanding Debt',
  },
  {
    id: 'collateralRatio',
    numeric: true,
    disablePadding: false,
    isSort: true,
    label: 'Collateral Ratio',
  },
  {
    id: 'guaranteeDays',
    numeric: true,
    disablePadding: false,
    isSort: true,
    label: 'Settlement Collateral Coverage',
  },
  {
    id: 'action',
    numeric: false,
    disablePadding: false,
    isSort: false,
    label: 'Action',
  },
];

function Collateral() {
  const {hashPowerTokenList, setRefresh, refresh} = useHashPowerToken()
  const [searchVal, setSearchVal] = useState();
  const [holderText, setPlaceholder] = useState<string>('Ticker')
  const [dataSource,setDataSource] = useState([])
  const onSearch = useCallback((event) => {
    setSearchVal(event.target.value);
    setDataSource(state=>{
      if (event.target.value) {
        return state.filter(it=> it.token.includes(event.target.value))
      }
      return hashPowerTokenList;
    })

  }, []);
  useEffect(()=>{
    if (hashPowerTokenList) {
      setDataSource(hashPowerTokenList)
    }
  },[hashPowerTokenList])

  const onSwitch = useCallback( (event, val)=>{
    if (val) {
      setDataSource(state=>{
        return state.filter(it=> Number(it.position)>0)
      })
    }else{
      setDataSource(hashPowerTokenList)
    }
  }, [hashPowerTokenList])


  return (
    <div style={{
    width: '100%', height: '100%',
    paddingRight: 100
  }}>
    <Title>Collateral</Title>
    <Card style={{height: 'calc(100% - 200px)'}} key={refresh}>
      <Search>
        <SearchIconWrapper>
          <SearchIcon/>
        </SearchIconWrapper>
        <StyledInputBase
          type="search"
          placeholder={holderText}
          inputProps={{'aria-label': 'search'}} value={searchVal} onInput={onSearch}
          onFocus={() => {
            setPlaceholder('')
          }}
          onBlur={() => {
            setPlaceholder('Ticker')
          }}
        />
        <SwitchButton onChange={onSwitch}/> <span className="opened">Opened</span>
      </Search>

      <Table
        key={refresh}
        dataSource={dataSource || []}
        columns={headCells}
        pagination={false}
        content={(row, index, labelId) => (
          <React.Fragment key={index}>
            <StyledTableCell component="th" id={labelId} scope="row"><img src={TokensIcon[row.ticker]} style={{display:"block", marginRight: 7,float:'left'}} alt='icon' />{row.ticker}</StyledTableCell>
            {/* @ts-ignore */}
            <StyledTableCell>{row.positionInfoValues&&Object.keys(row.positionInfoValues).length ? BigNumber.from(row.balance ) / BigNumber.from(10).pow(row.decimals) : 0}</StyledTableCell>
            <StyledTableCell>{row.positionInfoValues&&Object.keys(row.positionInfoValues).length ? row.positionInfoValues.cRate.includes('NaN')?0: (row.positionInfoValues.cRate === 'Infinity'? '+' : row.positionInfoValues.cRate+ "%") : 0}</StyledTableCell>
            <StyledTableCell>{row.positionInfoValues&&Object.keys(row.positionInfoValues).length && row.positionInfoValues.gDays ?( row.positionInfoValues.gDays === 'Infinity'? '+' : row.positionInfoValues.gDays) : 0} days</StyledTableCell>
            <StyledTableCell>
              <Action row={row} refresh={setRefresh}/>
            </StyledTableCell>
          </React.Fragment>
        )}
      />
    </Card>
  </div>


  );
}


export default Collateral;
