import React, { useContext } from 'react';
import {Link } from 'react-router-dom'
import {ComputedPowerTokenContext} from "contexts/ComputedPowerTokenContext";
import Card from "components/Card";
import {BigNumber} from "@ethersproject/bignumber";
import {IconButton} from "@mui/material";
import {useWeb3React} from "web3-react-core";
import Table, {StyledTableCell} from "components/Table"
import {formatWBTC} from "utils/formatTheValue";
import Action from "./Action";
import Title from "./Title"
import HistoryIcon from "./icon/History";
import {TokensIcon} from "../../../config/constants/types";
import useHashPowerToken from "../../../hooks/useHashPowerToken";

const columns =  [
  {
    id: 'ticker',
    numeric: false,
    disablePadding: true,
    isSort: true,
    label: 'Ticker',
  },
  {
    id: 'markPrice',
    numeric: false,
    disablePadding: true,
    isSort: true,
    label: 'Mark Price',
  },
  {
    id: 'HoldingsWallet',
    numeric: true,
    disablePadding: false,
    isSort: true,
    label: 'In Wallet',
  },
  {
    id: 'HoldingsAvatar',
    numeric: true,
    disablePadding: false,
    isSort: true,
    label: 'In Standard Collateral',
  },
  {
    id: 'claimableWallet',
    numeric: true,
    disablePadding: false,
    isSort: true,
    label: 'Claimable - In Wallet',
  },
  {
    id: 'claimableAvatar',
    numeric: true,
    disablePadding: false,
    isSort: true,
    label: 'Claimable - In Standard Collateral',
  },
  {
    id: 'action',
    numeric: false,
    disablePadding: false,
    isSort: false,
    label: 'Action',
  },
];
/*
* will
* add filter
*
*
* */
function HoldingsWallet({rewardsData}) {
  const {hashPowerTokenList, walletTokenBalance} = useHashPowerToken()
  const {account} = useWeb3React();
  return (
    <Card style={{marginBottom: 20}}>
      <Title>
        <h3>Holdings - Hashpower Token</h3>
        <Link to="/history/claimed/0" >
          <HistoryIcon /> Claimed History</Link>
      </Title>
        <Table
          columns={columns}
          dataSource={hashPowerTokenList || []}
          pagination={false}
          isMyPage
          content={(row,index, labelId)=>(
            <>
              <StyledTableCell style={{paddingLeft: 0}} component="th" id={labelId} scope="row"><img src={TokensIcon[row.ticker]} style={{display:"block", marginRight: 7,float:'left'}} alt='icon' />{row.ticker}</StyledTableCell>
              <StyledTableCell>{ (row.tokensValues)? formatWBTC(row.tokensValues[row.token]): 0} WBTC</StyledTableCell>
              <StyledTableCell>{ walletTokenBalance ?  walletTokenBalance[row.token] :0}</StyledTableCell>
              <StyledTableCell>{ Number(row.position) && row.avatarAssetList ?  row.avatarAssetList[row.token] :0}</StyledTableCell>
              {/*  @ts-ignore */}
              <StyledTableCell>{ rewardsData ? formatWBTC(rewardsData[row.token].walletRewards[0]): 0 } WBTC</StyledTableCell>
              <StyledTableCell>{ rewardsData ? formatWBTC(BigNumber.from(rewardsData[row.token].avatarRewards[0])): 0 } WBTC</StyledTableCell>
              <StyledTableCell>
                <Action row={row} address={account} rewardsData={rewardsData && rewardsData[row.token]} />
              </StyledTableCell>
            </>
          )}
        />
    </Card>
  );
}

export default HoldingsWallet;
