import React from 'react';
import {styled} from "@mui/material/styles";

const Wrapper = styled('div')`
  background: #DEF1FF;
  border-radius: 14px;
  padding: 13px 22px ;
  margin: 20px 0;
  table{
    width: 100%;
  }
  td{
    font-family: PingFangSC-Regular, sans-serif;
    font-weight: 400;
    font-size: 12px;
    padding: 5px 0;
  }
  .label{
    text-align: left;
    width: 30%;
  }
  .from,.to{
    width: 35%;
    line-height: 17px;
    text-align: right;
  }
  .to{
    font-family: PingFangSC-Medium, sans-serif;
    font-weight: 500;
    color: #000;
  }
  .borderTop {
    border-top: 0.5px solid rgba(0, 0, 0, 0.12);
  }
`

/*

const data = [
  ['Collateral Ratio', 100, 200],
  ['Collateral Ratio', 100, 200],
  ['Collateral Ratio', 100, 200],
]

*/

function DataWrapper(props) {
  const {data} = props;
  return (
    <Wrapper>
      <table>
        <tr>
          <td className="from" colSpan={2}>From</td>
          <td className="to">To</td>
        </tr>
        {
          data.map((item, index)=> (
            <tr className={item[3]? 'borderTop':''} key={item[0]}>
              <td className='label'>{item[0]}</td>
              <td className='from'>{item[1]}</td>
              <td className='to'>{item[2]}</td>
            </tr>
          ))
        }
      </table>
    </Wrapper>
  );
}

export default DataWrapper;
