import React, {useCallback, useContext, useEffect, useState} from 'react';
import {Typography} from '@mui/material/';
import Wallet from 'components/uikit/widgets/Menu/icons/Wallet'
import Methods from 'components/Methods'
import {useWeb3React} from "web3-react-core";
import {AvatarIcon, GuaranteeIcon} from "components/uikit";
import DataWrapper from "components/DataWrapper";
import {
  Wrapper,
  Title,
  Accordion,
  AccordionSummary,
  AccordionDetails,
  Approve,
  DataWrap,
  IconWrap,
  icon, FlexWrap, AlertButton
} from "components/util";
import {BigNumber} from "@ethersproject/bignumber";
import usePanel from "hooks/usePanel";
// eslint-disable-next-line import/named
import tokens, { poolData } from "config/constants/tokens";
import useToast from "hooks/useToast";
import contracts from "config/constants/contracts";
import {utils} from "ethers";
import {formatSnNumber, formatWBTC, toFixed,formatInfinity} from 'utils/formatTheValue'
import usePosContract from "../../../hooks/usePosContract";
import {useAppSelector} from "../../../state";
import useSystemParams from "../../../hooks/useSystemParams";
import useHashPowerToken from "../../../hooks/useHashPowerToken";
import {Constants} from "../../../config/constants/types";

const options = [
  ...poolData
]

const {fbtc20mint} = contracts

export default function WithdrawCollateral(props) {
  const {active, chainId, account} = useWeb3React()
  const { approveStatus, onApprove} = props;
  const {signer, getWBTC} = useSystemParams()
  const {walletTokenBalance, setRefresh} = useHashPowerToken()
  const currentCpt = useAppSelector(state=>state.collateralManage.currentCPT);
  const {avatarAssetList, positionInfoValues, position, allowedAssets, tokensValues, balance } = currentCpt
  const [m1LfVal,setM1LfVal] = useState<number>(0)
  const [m2LfVal,setM2LfVal] = useState<number>(0)
  const [currentParams,setCurrentParams] = useState<any>({
    nowGDays: 0,
    nowCRate: '0%'
  })
  const [futureParams,setFutureParams] = useState<any>({})

  const [tradingList, setTradingList] = useState([])
  const [leftSelectedToken,setLeftSelectedToken] = useState<any>({token:"WBTC"})
  const [rightSelectedToken,setRightSelectedToken] = useState<any>({token:"WBTC"})
  const [gDisStatus,setGDisStatus] = useState(true)
  const [aDisStatus,setADisStatus] = useState(true)

  const [approveBtnStatus,setApproveBtnStatus] = useState(false)
  const {toastError, toastSuccess, toastInfo} = useToast();
  const {expanded, handleChange} = usePanel();
  const posContract = usePosContract(active, signer)

  useEffect(()=>{
    if (m2LfVal>0) {
      setADisStatus(false)
    }
  }, [m2LfVal] )
  useEffect(()=>{
    if (m1LfVal>0) {
      setGDisStatus(false)
    }
  }, [m1LfVal] )
  // type 1/2  1=>methods1  2=> methods2
  const computed = useCallback((value, type)=>{
    // @ts-ignore
    const totalAssetsValues = (BigNumber.from(positionInfoValues.totalAssetsValues) * 1) - (Number(value) *  BigNumber.from(10).pow(tokens.WBTC.decimals) *1);
    /*
     * totalAssetsValues + value / shortBalance
     *
     * */
    if (type===1) {
      // @ts-ignore
      const interestGuaranteed = BigNumber.from(positionInfoValues.interestGuaranteed)

      setFutureParams(state=>({
        ...state,
        // @ts-ignore
        futureCRate1 : ( totalAssetsValues / BigNumber.from(positionInfoValues.shortBalanceValue) * 100)?.toFixed(2),
        // @ts-ignore
        futureGDays1: Math.floor( (interestGuaranteed - BigNumber.from(positionInfoValues.totalObligation)) / BigNumber.from(positionInfoValues.estimatedDailyDebt)),
      }))
    }
    if (type === 2) {
      console.log('computed_______>>>', value)
      // @ts-ignore
      const interestGuaranteed = (BigNumber.from(positionInfoValues.interestGuaranteed) * 1) - (Number(value) *  BigNumber.from(10).pow(tokens.WBTC.decimals) *1);

      setFutureParams(state=>({
        ...state,
        // @ts-ignore
        futureCRate2 : ( totalAssetsValues / BigNumber.from(positionInfoValues.shortBalanceValue) * 100)?.toFixed(2),
        // @ts-ignore
        futureGDays2: Math.floor( (interestGuaranteed - BigNumber.from(positionInfoValues.totalObligation)) / BigNumber.from(positionInfoValues.estimatedDailyDebt)),
      }))
    }

  },[positionInfoValues])
  // eslint-disable-next-line react-hooks/exhaustive-deps
  const iface1 = new utils.Interface(fbtc20mint.abi);


  const onGuaranteeCurrencyToWallet = useCallback(async ()=>{
    if (active && Number(position)) {
      try {
        // @ts-ignore
        const data = iface1.encodeFunctionData("moveGuarantee", [account, (BigNumber.from(10).pow(tokens.WBTC.decimals) * m1LfVal).toFixed(0)])
        const txInfo = await posContract.delegateCall(fbtc20mint.address[chainId], data);
        setGDisStatus(true)
        // const txInfo = await removeGuarantee('0xcD9720f75eBA3b343764814407363D22C9644D22', BigNumber.from(10).pow(tokens.WBTC.decimals).mul(m1LfVal));
        if (txInfo) {
          toastInfo('Submitted ', (
            <>
              <p style={{marginBottom: 24}}>Waiting for confirmation</p>
              <FlexWrap style={{flexDirection: 'row-reverse'}}>
                <AlertButton onClick={()=>{window.open(`https://kovan.etherscan.io/tx/${txInfo.hash}`)}}>View on Explorer</AlertButton>
              </FlexWrap>
            </>
          ))
          const result = await txInfo.wait();
          if (result) {
            setGDisStatus(false)
            toastSuccess('Submitted', (
              <>
                <FlexWrap style={{flexDirection: 'row-reverse'}}>
                  <AlertButton onClick={()=>{window.open(`https://kovan.etherscan.io/address/${position}`)}}>View on Explorer</AlertButton>
                </FlexWrap>
              </>
            ))
            setRefresh(state=>!state)
          }
        }
      } catch (e) {
        setGDisStatus(false)
        toastError('Failed', e.message)
      }
    }
  }, [active, iface1, account, m1LfVal, posContract, chainId, toastInfo, toastSuccess, position, toastError, setRefresh])



  const onTransfer = useCallback(async ()=>{
    if (active && account) {
      if (posContract) {
        // @ts-ignore
        const data6 = iface1.encodeFunctionData("withdrawAsset", [tokens[leftSelectedToken.token].address[chainId], account, formatSnNumber((BigNumber.from(10).pow(tokens[leftSelectedToken.token].decimals).toString() * m2LfVal).toFixed() * 1)])
        setADisStatus(true)
       try {
         const txInfo = await posContract.delegateCall(fbtc20mint.address[chainId], data6);
         setM2LfVal(0)
         if (txInfo) {
           toastInfo('Submitted ', (
             <>
               <p style={{marginBottom: 24}}>Waiting for confirmation</p>
               <FlexWrap style={{flexDirection: 'row-reverse'}}>
                 <AlertButton onClick={()=>{window.open(`https://kovan.etherscan.io/tx/${txInfo.hash}`)}}>View on Explorer</AlertButton>
               </FlexWrap>
             </>
           ))
           const result = await txInfo.wait();
           if (result) {
             setADisStatus(false)
             toastSuccess('Success','');
             setRefresh(state=>!state)
           }
         }
       } catch (e) {
          setADisStatus(false)
          toastError('Fail', e.message)
       }
      }

      // await transfer(managerInfo.avatar,  tokens.WBTC.address[chainId], account,BigNumber.from(10).pow(tokens.WBTC.decimals).mul(m2LfVal))
    }
  } , [account, active, chainId, iface1, m2LfVal, posContract, setRefresh,leftSelectedToken, toastInfo, toastSuccess, toastError])

  const onInput = (e)=>{
    setM1LfVal(e.target.value);
    if (e.target.value>0){
      setGDisStatus(false)
    }else{
      setGDisStatus(true)
    }
    console.log(e.target.value)
    computed(e.target.value, 2)
  }
  const onMax = (type)=>{
    if (positionInfoValues) {
      const {estimatedDailyDebt, totalObligation, interestGuaranteed, totalAssetsValues, collateralValue,shortBalanceValue} = positionInfoValues

      const maxMoveAmount = formatWBTC(totalAssetsValues - shortBalanceValue*1.8)
      if (type===2) {
        console.log('maxMoveAmount>>>>>', maxMoveAmount)
        if (maxMoveAmount > formatWBTC(collateralValue)) {
          const _value = toFixed(maxMoveAmount - formatWBTC(collateralValue), leftSelectedToken.decimals)
          setM1LfVal(_value)
          computed(_value, 2)
        } else if (maxMoveAmount > formatWBTC(interestGuaranteed)) {
          const _value = toFixed(formatWBTC(interestGuaranteed), leftSelectedToken.decimals)
          setM1LfVal(_value)
          computed(maxMoveAmount, 2)
        } else if(maxMoveAmount <  formatWBTC(interestGuaranteed)) {
          const _value = toFixed(maxMoveAmount, leftSelectedToken.decimals)
          setM1LfVal(_value)
          computed(_value, 2)
        } else {
          setM1LfVal(0)
          computed(0, 2)
        }
      }
      if (type===1 && avatarAssetList) {
        if (leftSelectedToken.token !== 'WBTC') {
          const maxMoveUSDTAmount = getWBTC(leftSelectedToken);
          if (maxMoveAmount > maxMoveUSDTAmount) {
            setM2LfVal(avatarAssetList[leftSelectedToken.token])
            computed(maxMoveUSDTAmount, 1)
          } else{
            setM2LfVal(maxMoveAmount)
            computed(maxMoveAmount, 1)
          }
        } else if (maxMoveAmount > avatarAssetList[leftSelectedToken.token]) {
          setM2LfVal(avatarAssetList[leftSelectedToken.token])
          computed(avatarAssetList[leftSelectedToken.token], 1)
        }else if (maxMoveAmount < avatarAssetList[leftSelectedToken.token]) {
          setM2LfVal(maxMoveAmount)
          computed(maxMoveAmount, 1)
        } else {
          setM2LfVal(0)
          computed(0, 1)
        }

      }
      else{
        setM2LfVal(0)
        computed(0, 1)
      }

    }

  }
  /**
   * getWalletTokenBalance
   * */


  useEffect(()=>{
    if (active && Number(position) && positionInfoValues) {
      setCurrentParams({
        nowGDays: Number(positionInfoValues.gDays) === Infinity ? '+' : positionInfoValues.gDays,
        nowCRate: Number(positionInfoValues.cRate) === Infinity ? '+' : `${positionInfoValues.cRate}%`,
      })

      computed(0, 1)
      computed(0, 2)
    }
  }, [active, computed, position, positionInfoValues])



  useEffect(()=>{
    if (active && Number(position) && walletTokenBalance) {
      const list = []
      // eslint-disable-next-line no-restricted-syntax
      for (const [token, data] of Object.entries(tokens)) {
        const item:any = {...data}
        if (allowedAssets.includes(data.address[42].toLocaleUpperCase())) {
          item.balance = formatSnNumber(avatarAssetList[token])
          item.amount = formatSnNumber(avatarAssetList[token])
          item.avatarBalance = formatSnNumber(avatarAssetList[token])
          item.token =  token
          item.rightOptionList = []
          item.rightOptionList.push({
            address: item.address,
            balance: formatSnNumber(walletTokenBalance[token]),
            'token': token
          })
          list.push(item)
        }
      }
      setTradingList(list)
      setLeftSelectedToken(list[0])
      setRightSelectedToken(list[0].rightOptionList[0])
    }
  }, [active, allowedAssets, avatarAssetList, position, walletTokenBalance])




  return (
    <Wrapper>
      <Title>Withdraw Collateral</Title>
      <Accordion id="panel13"  expanded={expanded === 'panel13'} onChange={handleChange('panel13')}>
        <AccordionSummary aria-controls="panel1d-content" >
          <Typography>Method 1. Withdraw asset from Standard Collateral to my wallet</Typography>
        </AccordionSummary>
        <AccordionDetails>
          <Methods rightOptions={tradingList}
            SelectIcon={AvatarIcon} options={tradingList}
            leftSelectValue={leftSelectedToken?leftSelectedToken.token: ''}
            rightSelectValue={rightSelectedToken?rightSelectedToken.token: ''}
            rightBalance={rightSelectedToken?rightSelectedToken.balance: ''}
            leftBalance={leftSelectedToken?leftSelectedToken.balance: ''}
            leftInputValue={m2LfVal}
            rightInputValue={m2LfVal}
            leftChange={(selected)=>{
              setLeftSelectedToken(selected)
              setRightSelectedToken(selected.rightOptionList[0])
              // @ts-ignore
              computed(leftSelectedToken.balance * (BigNumber.from(tokensValues[leftSelectedToken.token]) / BigNumber.from(10).pow(tokens.WBTC.decimals)))
            }}
            selectInput={(e)=>{
              // @ts-ignore
              setM2LfVal(e.target.value);

              if (e.target.value>0){
                setADisStatus(false)
              }else{
                setADisStatus(true)
              }
              // @ts-ignore
              computed(e.target.value * (BigNumber.from(tokensValues[leftSelectedToken.token]) / BigNumber.from(10).pow(tokens.WBTC.decimals)), 1)
            }}
            onMax={()=>{
              onMax(1)
            }}
            RightIcon={Wallet}/>
          <DataWrapper
            data={[
              // eslint-disable-next-line no-sequences,no-nested-ternary
              ['Collateral Ratio', currentParams.nowCRate.includes('NaN')? '0%': currentParams.nowCRate, futureParams.futureCRate1 ? formatInfinity(futureParams.futureCRate1).toString().includes('') ? formatInfinity(futureParams.futureCRate1) : `${futureParams.futureCRate1}%` : '0%'],

              // eslint-disable-next-line no-sequences,no-nested-ternary
              ['Settlement Collateral Coverage', `${ currentParams.nowGDays || 0} days`, `${futureParams.futureGDays1 ? formatInfinity(futureParams.futureGDays1) : '0'} days`]
            ]}
          />
          {
            approveStatus ?
              <Approve style={{boxShadow: 'none'}} disabled={aDisStatus} onClick={onTransfer}>Submit</Approve>:
              <Approve style={{boxShadow: 'none'}} disabled={approveBtnStatus} onClick={()=>{
                onApprove((state)=>{
                  setApproveBtnStatus(state)
                })
              }
              }>Approve</Approve>
          }
        </AccordionDetails>
      </Accordion>
      <Accordion  id="panel14" expanded={expanded === 'panel14'} onChange={handleChange('panel14')}>
        <AccordionSummary aria-controls="panel1d-content">
          <Typography>Method 2. Withdraw asset from Settlement Collateral to my wallet</Typography>
        </AccordionSummary>
        <AccordionDetails>
          <Methods
            selectInput={onInput}
            onMax={()=>{
              onMax(2)
            }}
            SelectIcon={GuaranteeIcon} options={options} rightBalance={walletTokenBalance? walletTokenBalance.WBTC : 0} rightOptions={options}
            rightSelectedValue={42} rightSymbol="WBTC" selectValue={42} selectedBalance={100} selectedSymbol="WBTC"
            leftBalance={Number(position) && positionInfoValues? formatWBTC(BigNumber.from(positionInfoValues.guaranteeValues)) : 0}
            leftInputValue={m1LfVal}
            rightInputValue={m1LfVal}
            RightIcon={Wallet}/>
          <DataWrapper
            data={[
              // eslint-disable-next-line no-sequences,no-nested-ternary
              ['Collateral Ratio', currentParams.nowCRate.includes('NaN')? '0%': currentParams.nowCRate, futureParams.futureCRate2 ? formatInfinity(futureParams.futureCRate2).toString().includes('') ? formatInfinity(futureParams.futureCRate2) : `${futureParams.futureCRate2}%` : '0%'],

              // eslint-disable-next-line no-sequences,no-nested-ternary
              ['Settlement Collateral Coverage',  `${currentParams.nowGDays || 0} days`, `${futureParams.futureGDays2 ? formatInfinity(futureParams.futureGDays2) : '0'} days`]
            ]}
          />
          {
            approveStatus ?
              <Approve style={{boxShadow: 'none'}} disabled={gDisStatus} onClick={onGuaranteeCurrencyToWallet}>Submit</Approve>:
              <Approve style={{boxShadow: 'none'}} disabled={approveBtnStatus} onClick={()=>{
                onApprove((state)=>{
                  setApproveBtnStatus(state)
                })
              }
              }>Approve</Approve>
          }
        </AccordionDetails>
      </Accordion>
    </Wrapper>
  );
}
