import React, {useCallback, useEffect, useState} from 'react';
import {useWeb3React} from "web3-react-core";
import {getContract, getSigner} from "utils/web3React";
import tokens, {tokenType} from "config/constants/tokens";
import {OpenStatus, tokensSymbol} from "config/constants/types";
import contracts from "config/constants/contracts";
import PositionABI from "config/abi/Position.json";
import {BigNumber} from "@ethersproject/bignumber";
import {formatWBTC} from 'utils/formatTheValue'
import MinerABI from 'config/abi/Miner.json'
import oracleHub from 'utils/oracleHub'
import {useGetTokenBalance} from "../hooks/useManager";
import {useUpdataCurrentCPT} from "../state/hooks";
import {Fbtc20Icon} from "../components/uikit";
import {SupportedChainId} from "../config/constants/chains";


const {manager, matrix, helper} = contracts

const ComputedPowerTokenContext = React.createContext(null)

function ComputedPowerTokenProvider({children}) {
  const {active, chainId, library, account} = useWeb3React();
  const [cptList, setCptList] = useState([]); // computed token list->> cptList
  //  Convenient to get specified cpt
  const [cptMapList, setCptMapList] = useState(null);
  const [refresh,setRefresh] = useState(false)
  const [signer, setSigner] = useState(null);
  const [walletTokenBalance, setWalletTokenBalance] = useState(null);
  // const [oracleContract, setOracleContract] = useState(null)
  const [accountContract, setAccountContract] = useState(null)
  const [matrixContract, setMatrixContract] = useState(null)
  const [helperContract, setHelperContract] = useState(null)
  const [loadingStatus,setLoadingStatus] = useState(true)
  const getTokenBalance = useGetTokenBalance(library, active, chainId);
  const {changeCPT} = useUpdataCurrentCPT()
    // eslint-disable-next-line consistent-return
  const queryValues = useCallback(async (position, token, mark) => {
      if (active && position) {
        let result;
        if (mark === 'WBTC') {
          result = await matrixContract.queryValues([
            [tokens[token].address[chainId], BigNumber.from(10).pow(tokens[token].decimals), true],
            [tokens.USDT.address[chainId], BigNumber.from(10).pow(tokens.USDT.decimals), true]
          ], tokens.WBTC.address[chainId], position);
        }
        if (result) {
          return {
            [token]: result[0],
            [mark === 'WBTC'? 'USDT': 'WBTC']: result[1],
            WBTC: BigNumber.from(10).pow(tokens.WBTC.decimals)    // 1wbtc ~= ?USDT
          }
        }
        return {
          [token]: null,
          [tokensSymbol.USDT]: null,
          [tokensSymbol.WBTC]:  0
        }
      }
    }, [active, chainId, matrixContract])

  // eslint-disable-next-line consistent-return
  const queryAssets = useCallback(async (position, token) => {
    if (active && matrixContract && position) {
      return Promise.all([
        matrixContract.queryAssetsEff(tokens[token].address[chainId], tokens.WBTC.address[chainId], position),
        matrixContract.queryAllowedAssets(tokens[token].address[chainId])
      ])
    }
  }, [active, chainId, matrixContract])

  // eslint-disable-next-line consistent-return
  const pendingReward = useCallback(async (abi,cAddr, token,address, decimals,balance, lastModifiedEpoch?,lastModifiedTime? ,accAmountTime?)=>{
    if (library) {
      const _signer = getSigner(library, address);
      const updater = getContract(_signer, abi, cAddr);
      const miner = getContract(_signer, MinerABI, '0x5Ddb971a703Cc55D03692F9ee995587d9E3f8ECD');

      if (updater && walletTokenBalance && miner) {
        const holderInfos = await miner.holderInfos(address);

        if (lastModifiedEpoch) {
          // @ts-ignore
          return updater.pendingReward(balance, holderInfos[0], holderInfos[1], holderInfos[2]);
        }
        // @ts-ignore
        return updater.pendingReward(BigNumber.from(10).pow(decimals) * balance, holderInfos[0], holderInfos[1], holderInfos[2]);
      }
    }

    // const contract = getContract(signer, abi, )
  }, [library, walletTokenBalance])

  // eslint-disable-next-line consistent-return
  const estimateDailyReward = useCallback(async (abi, cAddr, balance)=>{
    if (signer) {
      const updater = getContract(signer, abi, cAddr);
      if (updater) {
        // eslint-disable-next-line no-return-await
        // @ts-ignore
        return updater.estimatedDailyReward(balance)
      }
    }
  }, [signer])


  // eslint-disable-next-line consistent-return
  const getPositionValues = useCallback(async (position, cptBalance) => {
    if (active && helperContract && position) {
      const positionValues = await helperContract.getPositionValueInfo(position, cptBalance);
      const data = new Map(Object.entries(positionValues));
      // @ts-ignore
      const totalObligation = data.get('obligation').add(data.get('epochDebt')).add(data.get('estimatedObligationToday'));
      const interestGuaranteed = data.get('interestGuaranteed');
      const collateralValue = data.get('collateralValue');
      let cRate: null;
      let totalAssetsValues: null;
      if (totalObligation.toString() * 1) {
        // @ts-ignore
        totalAssetsValues = collateralValue.add(interestGuaranteed).sub(totalObligation)
        // @ts-ignore
        cRate = (totalAssetsValues / data.get('shortBalanceValue') * 100).toFixed(2)
      } else {
        // @ts-ignore
        totalAssetsValues = collateralValue.add(interestGuaranteed)
        // @ts-ignore
        cRate = (totalAssetsValues / data.get('shortBalanceValue') * 100).toFixed(2)
      }

      // @ts-ignore
      data.set('totalAssetsValues', totalAssetsValues)
      data.set('totalObligation', totalObligation)
      // @ts-ignore
      data.set('cRate', cRate)

      // @ts-ignore
      if ((interestGuaranteed - totalObligation) === 0) {
        // @ts-ignore
        data.set("gDays", '0')
        // @ts-ignore
      }else if ( data.get('estimatedDailyDebt').toNumber() === 0) {
        data.set("gDays", 'Infinity')
        // @ts-ignore
      }else {
        // @ts-ignore
        data.set("gDays", Math.floor((interestGuaranteed - totalObligation) / data.get('estimatedDailyDebt')))
      }
      return Object.fromEntries(data.entries())
    }
  }, [active, helperContract])

  const initData = useCallback(async () => {
    if (active && chainId && chainId === 42) {
      setLoadingStatus(true)
      const data = {};
      if (accountContract) {
        try {
          // eslint-disable-next-line ,no-restricted-syntax
          for (const tokensKey in tokens) {
            if (tokens[tokensKey].type === tokenType.CPT) {
              // eslint-disable-next-line no-await-in-loop
              const position = await accountContract.positions(account, tokens[tokensKey].address[chainId]);
              const posContract = getContract(signer, PositionABI, position);
              let cptBalance = 0;
              let tokensValues = {};
              let positionAssetsTotal = 0;
              let allowedAssets = [];
              let positionInfoValues = {};
              let avatarAssetList:any
              let lastModifiedEpoch
              let lastModifiedTime
              let accAmountTime
              let walletRewards = []
              let avatarRewards = []
              let avatarEstimateDailyReward = 0
              let walletEstimateDailyReward = 0
              let dailyObligation = 0
              // let tokensUSDTValues = {}
              if (posContract && Number(position)) {
                // eslint-disable-next-line no-await-in-loop
                cptBalance = await posContract.balance()
                // eslint-disable-next-line no-await-in-loop
                lastModifiedEpoch = await posContract.lastModifiedEpoch();
                // eslint-disable-next-line no-await-in-loop
                lastModifiedTime = await posContract.lastModifiedTime();
                // eslint-disable-next-line no-await-in-loop
                accAmountTime = await posContract.accAmountTime();


                // Query a fixed quantity
                // eslint-disable-next-line no-await-in-loop
                dailyObligation = await estimateDailyReward(
                  tokens[tokensKey].blockUpdater.abi,
                  tokens[tokensKey].blockUpdater.address[chainId],
                  BigNumber.from(10).mul(BigNumber.from(10).pow(tokens[tokensKey].decimals)))
              }
              if (Number(position)) {

                // eslint-disable-next-line no-await-in-loop
                tokensValues = await queryValues(position, tokensKey, 'WBTC');
                // eslint-disable-next-line no-await-in-loop
               // await queryValues(position, tokensKey, 'USDT');
                // eslint-disable-next-line no-await-in-loop
               // await queryValues(position, tokensKey, tokensKey);
                // eslint-disable-next-line no-await-in-loop
                [positionAssetsTotal, allowedAssets] = await queryAssets(position, tokensKey);
                allowedAssets = allowedAssets.map(item=>item.toLocaleUpperCase())
                // eslint-disable-next-line no-await-in-loop
                positionInfoValues = await getPositionValues(position, cptBalance);
                // eslint-disable-next-line no-await-in-loop
                avatarAssetList = await getTokenBalance(position)
                // eslint-disable-next-line @typescript-eslint/no-unused-vars,no-await-in-loop
                avatarRewards = await pendingReward(
                  tokens[tokensKey].blockUpdater.abi,
                  tokens[tokensKey].blockUpdater.address[chainId],
                  tokensKey,
                  position,
                  tokens[tokensKey].decimals,
                  avatarAssetList[tokensKey]);
                // @ts-ignore
                // eslint-disable-next-line no-await-in-loop
                avatarEstimateDailyReward = await estimateDailyReward(
                  tokens[tokensKey].blockUpdater.abi,
                  tokens[tokensKey].blockUpdater.address[chainId],
                  // @ts-ignore
                  avatarAssetList[tokensKey]* BigNumber.from(10).pow(tokens[tokensKey].decimals))
                // eslint-disable-next-line no-await-in-loop
                walletEstimateDailyReward = await estimateDailyReward(
                  tokens[tokensKey].blockUpdater.abi,
                  tokens[tokensKey].blockUpdater.address[chainId],
                  // @ts-ignore
                  walletTokenBalance[tokensKey] * BigNumber.from(10).pow(tokens[tokensKey].decimals))

              }
              // eslint-disable-next-line no-prototype-builtins
              if (walletTokenBalance.hasOwnProperty(tokensKey)) {
                // eslint-disable-next-line no-await-in-loop
                walletRewards = await pendingReward(
                  tokens[tokensKey].blockUpdater.abi,
                  tokens[tokensKey].blockUpdater.address[chainId],
                  tokensKey,
                  account,
                  tokens[tokensKey].decimals,
                  walletTokenBalance[tokensKey]);
              }
              // 1WBTC  token
              const WBTCRouter = {}

              // eslint-disable-next-line no-restricted-syntax
              for (const token in tokensValues) {
                if (Object.hasOwnProperty.call(tokensValues, token)) {
                  const wbtc = tokensValues[token];
                  // @ts-ignore
                  WBTCRouter[token] = 1 / (wbtc / BigNumber.from(10).pow(tokens.WBTC.decimals));
                }
              }



              data[tokensKey] = {
                ...tokens[tokensKey],
                token: tokensKey,
                ticker: tokensKey,
                status: Number(position)>0 ? OpenStatus.opened : OpenStatus.notOpen,
                position,
                tokensValues,
                avatar: position,
                cptBalance,
                positionAssetsTotal,
                allowedAssets,
                positionInfoValues,
                avatarAssetList,
                walletRewards,
                avatarRewards,
                avatarEstimateDailyReward,walletEstimateDailyReward,
                dailyObligation,
                WBTCRouter
                // tokensUSDTValues
              }
            }
          }
          // Serialization of saved data prevents repeated component refreshes when referenced by useEffect
          setCptMapList(JSON.stringify(data))
        } catch (e) {
          // eslint-disable-next-line no-console
          console.debug(e.message)
        }
      }
    }
  }, [account, accountContract, active, chainId, estimateDailyReward, getPositionValues, getTokenBalance, pendingReward, queryAssets, queryValues, signer, walletTokenBalance])

  const computed = useCallback((_mintAmount, cptInfo, callback) => {
    const {positionInfoValues, token, tokensValues, cptBalance, position, dailyObligation} = cptInfo;
    if (cptInfo && Number(position)) {
      const dividend = BigNumber.from(10).pow(cptInfo.decimals);
      // @ts-ignore
      const allBalance =  BigNumber.from(positionInfoValues.interestGuaranteed) - BigNumber.from(positionInfoValues.totalObligation);
      let mintAllAmount: BigNumber;
      if (_mintAmount > 0) {
        // @ts-ignore
        mintAllAmount = BigNumber.from(cptBalance) / dividend + Number(_mintAmount)
      } else {
        // @ts-ignore
        mintAllAmount =  BigNumber.from(cptBalance) / dividend
      }// @ts-ignore
      const estimatedDailyDebtPerDebt = BigNumber.from(dailyObligation) / 10;


      // @ts-ignore
      const gDays = Math.floor(allBalance / Math.floor(estimatedDailyDebtPerDebt * mintAllAmount))

      // @ts-ignore
      const shortBalance = BigNumber.from(tokensValues[token]) * mintAllAmount;
      // @ts-ignore
      const ratio = (BigNumber.from(positionInfoValues.totalAssetsValues) / shortBalance * 100).toFixed(2)

      // eslint-disable-next-line no-unused-expressions
      callback && callback(ratio, gDays, mintAllAmount);
    }

  }, [])


  useEffect(() => {
    if (active && account) {
      (async () => {
        try {
          const walletAssets = await getTokenBalance(account);
          setWalletTokenBalance(walletAssets)
        }catch (e) {
          setWalletTokenBalance({
            WBTC:0,
            USDT:0,
            'F(BTC,20)':0
          })
        }

      })();
    }
    return ()=>{
      setWalletTokenBalance(null);
    }
  }, [account, active, changeCPT, getTokenBalance, refresh])

  useEffect(() => {
    if (walletTokenBalance && chainId === SupportedChainId.KOVAN) {
      initData().then(()=>{
      if (cptMapList) {
        const mapList = JSON.parse(cptMapList);
        const list = []
        // @ts-ignore
        // eslint-disable-next-line guard-for-in,no-restricted-syntax
        for (const token in mapList) {
          list.push({...mapList[token], icon: Fbtc20Icon});
        }
        const copydata = {...list[0]};
        // delete copydata.icon
        changeCPT(copydata)
        setCptList(list);
        setLoadingStatus(false)
      }
    })
    }
    const timer = setTimeout(()=>{
      setLoadingStatus(false)
    },15000)

    return ()=>{
      clearTimeout(timer)
    }
  }, [changeCPT, cptMapList, initData, refresh, walletTokenBalance, account, chainId])

  useEffect(() => {
    if (active && account && chainId && chainId === SupportedChainId.KOVAN) {
      const Signer = getSigner(library, account);
      const accContract = getContract(Signer, manager.accountRegister.abi, manager.accountRegister.address[chainId])
      // const orContract = getContract(Signer, manager.oracleRegister.abi, manager.oracleRegister.address[chainId])
      const _matrixContract = getContract(Signer, matrix.abi, matrix.address[chainId])
      const _helper = getContract(Signer, helper.abi, helper.address[chainId])
      setSigner(Signer);
      // setOracleContract(orContract)
      setAccountContract(accContract)
      setMatrixContract(_matrixContract)
      setHelperContract(_helper)
    }
    // getPosition();
  }, [account, active, chainId, library])




  return (
    <ComputedPowerTokenContext.Provider value={{
      computed,
      cptList,
      cptMapList,
      accountContract,
      matrixContract,
      helperContract,
      setCptMapList,
      walletTokenBalance,
      signer,
      refresh,
      setRefresh,
      loadingStatus,
      getTokenBalance
    }}>
      {children}
    </ComputedPowerTokenContext.Provider>
  );
}

export {ComputedPowerTokenContext, ComputedPowerTokenProvider};
