import React, {useCallback, useContext, useEffect, useState} from 'react';
import {BigNumber} from "@ethersproject/bignumber";
import {ComputedPowerTokenContext} from "contexts/ComputedPowerTokenContext";
import {formatWBTC, toFixed} from "utils/formatTheValue";
import {InputBase, IconButton} from "@mui/material";
import SearchIcon from '@mui/icons-material/Search';
import {styled} from '@mui/material/styles';
import {useWeb3React} from "web3-react-core";
import { BG_COMMON_COLOR } from 'components/uikit/widgets/Menu/config';
import Card from 'components/Card'
import SwitchButton from "components/Switch";
import MainTitle from "components/util/MainTitle";
import Table, { StyledTableCell } from 'components/Table'
import {UniswapIcon} from 'components/uikit'
import Action from './components/Action'
import {SupportedChainId} from "../../config/constants/chains";
import {TokensIcon} from "../../config/constants/types";
import tokens from "../../config/constants/tokens";
import useSystemParams from "../../hooks/useSystemParams";
import useHashPowerToken from "../../hooks/useHashPowerToken";
import {HashPowerTokenProvider} from "../../contexts";
import useComputedPrice from "../../hooks/useComputedPrice";

export const Search = styled('div')(() => ({
  display: 'flex',
  alignItems: 'center',
  justifyContent: 'space-between',
  position: 'relative',
  boxSizing: 'border-box',
  borderRadius: 11,
  height: 44,
  marginBottom: 34,
  '.label': {
    fontFamily: 'PingFangSC-Regular, sans-serif',
    fontSize: 12,
    lineHeight: '16.8px',
    color: '#000'
  }
}));

export const SearchIconWrapper = styled('div')(({theme}) => ({
  padding: theme.spacing(0, 2),
  height: '100%',
  position: 'absolute',
  pointerEvents: 'none',
  display: 'flex',
  alignItems: 'center',
  justifyContent: 'center',
}));

export const StyledInputBase = styled(InputBase)(({theme}) => ({
  color: 'inherit',
  width: '100%',
  height: '44px',
  '& .MuiInputBase-input': {
    padding: theme.spacing(1, 1, 1, 0),
    paddingLeft: `calc(1em + ${theme.spacing(4)})`,
    background: `${BG_COMMON_COLOR}`,
    boxSizing: "border-box",
    borderRadius: "11px",
    border: '1px solid rgba(207, 216, 226, 0.5)',
    transition: theme.transitions.create('width'),
    width: '100%',
    height: '100%',
    caretColor: '#6ED6F3',
    '&::-webkit-search-cancel-button': {
      height: "1em",
      width: "1em",
      borderRadius: '50em',
      background: 'url(https://pro.fontawesome.com/releases/v5.10.0/svgs/solid/times-circle.svg) no-repeat 50% 50%',
      backgroundSize: 'contain',
      opacity: "0.3",

    }
  }
}));

const headCells = [
  {
    id: 'ticker',
    numeric: false,
    disablePadding: true,
    isSort: true,
    label: 'Ticker',
  },
  {
    id: 'isHoldingStatus',
    numeric: true,
    disablePadding: false,
    isSort: true,
    label: 'Buy & Sell',
  },
  {
    id: 'markPrice',
    numeric: false,
    disablePadding: true,
    isSort: true,
    label: 'Market Price',
  },
  {
    id: 'holdings',
    numeric: true,
    disablePadding: false,
    isSort: true,
    label: 'Holding - In Wallet',
  },
  {
    id: 'outstandingDebt',
    numeric: true,
    disablePadding: false,
    isSort: true,
    label: 'Outstanding Debt',
  },
  {
    id: 'action',
    numeric: false,
    disablePadding: false,
    isSort: false,
    label: 'Action',
  },
];


function Trade() {
  const [searchVal, setSearchVal] = useState('');
  const [holderText, setPlaceholder] = useState<string>('Ticker')
  const {chainId} = useWeb3React()
  const {pairWBTCAndFBTC} = useSystemParams();
  const {hashPowerTokenList, walletTokenBalance} = useHashPowerToken()
  const [collateralList,setCollateralList] = useState(hashPowerTokenList || [])

  const onSearch = useCallback((event) => {
    setSearchVal(event.target.value);
    setCollateralList(state=>{
      if (event.target.value) {
        return state.filter(it=> it.token.includes(event.target.value))
      }
      return hashPowerTokenList;
    })

  }, [hashPowerTokenList]);

  const onSwitch = useCallback( (event, val)=>{
    if (val) {
      setCollateralList(state=>{
        return state.filter(it=> Number(it.position)>0)
      })
    }else{
      setCollateralList(hashPowerTokenList)
    }
  }, [hashPowerTokenList])


  const computedPrice = useComputedPrice()


  useEffect(()=>{
    if (hashPowerTokenList) {
      setCollateralList(hashPowerTokenList)
    }
  }, [hashPowerTokenList])

  return (
    <div style={{
      width: '100%', height: '100%',
      paddingRight: 100
    }}>
      <MainTitle style={{marginBottom:14}}>Trade</MainTitle>
      <Card style={{height: 'calc(100% - 200px)'}}>
        <Search>
          <SearchIconWrapper>
            <SearchIcon/>
          </SearchIconWrapper>
          <StyledInputBase
            type="search"
            placeholder={holderText}
            inputProps={{'aria-label': 'search'}} value={searchVal} onInput={onSearch}
            onFocus={() => {
              setPlaceholder('')
            }}
            onBlur={()=>{
              setPlaceholder('Ticker')
            }}
          />
          <SwitchButton onChange={onSwitch} /> <span className="label">Position</span>
        </Search>
        <Table
          dataSource={collateralList}
          columns={headCells}
          pagination={false}
          content={(row,index, labelId)=>(
            <>
              <StyledTableCell component="th" id={labelId} scope="row"><img src={TokensIcon[row.ticker]} style={{display:"block", marginRight: 7,float:'left'}} alt='icon' />{row.ticker}</StyledTableCell>
              <StyledTableCell> <IconButton onClick={()=>{
                window.open(`https://app.uniswap.org/#/swap?&outputCurrency=${tokens.WBTC.address[42]}&inputCurrency=${row.address[chainId]}&chain=${SupportedChainId[chainId]}`)
              }}><UniswapIcon /></IconButton> </StyledTableCell>
              <StyledTableCell>{ pairWBTCAndFBTC && row.token ? toFixed( computedPrice({
                outputToken:{
                  token: "WBTC",
                  decimals: 8,
                },
                inputToken: {
                  token: row.token,
                  decimals: 6
                }
              }, 1), 'WBTC') : 0} WBTC</StyledTableCell>
              <StyledTableCell>{ walletTokenBalance ?  walletTokenBalance[row.token] :0}</StyledTableCell>
              {/*  @ts-ignore */}
              <StyledTableCell>{ BigNumber.from(row.balance) / BigNumber.from(10).pow(row.decimals) }</StyledTableCell>
              <StyledTableCell>
                <Action row={row}/>
              </StyledTableCell>
            </>
          )}
        />
      </Card>
    </div>
  );
}

export default React.memo(Trade);
