import React, {useContext, useEffect, useState} from 'react'
import {useWeb3React} from "web3-react-core";
import {AntTabs, AntTab} from 'components/Tabs/index'
import {styled} from '@mui/material/styles'
import {Grid} from '@mui/material'
import {ManagerContext} from "contexts/ManagerContext";
import {PositionContextProvider} from "contexts/PositionContext";
import MyCollateral from 'components/MyCollateral'
import {ScrollWrapper} from 'components/util'
import Mint from './components/Mint'
import Neutralize from './components/Neutralize'
import {ComputedPowerTokenContext} from "../../contexts/ComputedPowerTokenContext";
import {HashPowerTokenContext, HashPowerTokenProvider} from "../../contexts";

const Title = styled(AntTab)`
  font-size: 32px;
  font-weight: 550;
  color: rgba(0, 0, 0, 0.5);
  padding-left: 0;
  padding-right: 0;
  margin-right: 40px;

  &.Mui-selected {
    color: #000;
  }
`

const Container = styled('div')`
  min-width: 1024px;
`
const Wrapper = styled(AntTabs)`
  margin: 0;
  margin-bottom: 18px;
`

function MintPage(props) {
  const {history,location, match} = props;
  const {active} = useWeb3React();
  const [value, setValue] = useState(0)
  const handleChange = (event: React.SyntheticEvent, newValue: number) => {
    setValue(newValue);
    history.push(`/mint/${newValue}`)
  }

  useEffect(() => {
    if (active) {
      if (Number(match.params.id) === 0) {
        setValue(0)
      }
      if (Number(match.params.id) === 1) {
        setValue(1)
      }
    }
  }, [active, match])

  return (
    <div style={{paddingRight: 44, overflow:"auto"}}>

      <Wrapper value={value} onChange={handleChange}>
        <Title label="Mint"/>
        <Title label="Neutralize"/>
      </Wrapper>
      <Container>
        <Grid container spacing={2}>
          <Grid item xs={8}>
            <ScrollWrapper>
              <Mint hidden={value !== 0}/>
              <Neutralize hidden={value !== 1}/>
            </ScrollWrapper>
          </Grid>
          <Grid item xs={4}>
            <MyCollateral />
          </Grid>
        </Grid>
      </Container>
    </div>
  )
}

export default MintPage
