export default function  MyCollateral(props) {
  const {pageType} = props
  const currentCpt = useAppSelector(state=> state.collateralManage.currentCPT);
  const {position, balance, decimals, positionInfoValues,address} = currentCpt;
  const {chainId} = useWeb3React()
  return (
    <Wrapper>
      <LeftWrap>
        <TitleContent>{pageType === 'Liq' ? 'The Collateral' :'My Collateral'} </TitleContent>
        <AssetListWrap>
          <div className="total">
            <span>Total Collateral Value</span>
            <span> {currentCpt && Number(currentCpt.avatar) ? formatWBTC(BigNumber.from(currentCpt.positionInfoValues.totalAssetsValues)) : 0} WBTC</span>
          </div>
          <div className="guarantee">
            <GuaranteeIcon className='icon' />
            <div>
              <span className="title">Settlement Collateral</span>
               <span> {currentCpt && Number(currentCpt.avatar) ? formatWBTC(BigNumber.from(currentCpt.positionInfoValues.interestGuaranteed)) : 0} WBTC</span>
            </div>
          </div>
          <div className="avatar">
            <AvatarIcon className="icon" />
            <div>
              <span className="title">Standard Collateral</span>
              {/* @ts-ignore */}
              <CopyToClipboard className="avatar_info" toCopy={currentCpt? currentCpt.position: ''}>
                <span>{currentCpt && Number(currentCpt.avatar) ? `${currentCpt.position.slice(0, 3)}..${currentCpt.position.slice(5, 8)}` : ''}</span>
              </CopyToClipboard>
            </div>
          </div>
          <List className="token_list">
            <ListItem>
              <WBTCIcon className='icon' />
              <div className="currency">
                <span className="symbol">WBTC</span>
                <span className="balance">{currentCpt && Number(currentCpt.avatar)? currentCpt.avatarAssetList.WBTC: 0}</span>
              </div>
            </ListItem>
            <ListItem>
              <USDTIcon className='icon' />
              <div className="currency">
                <span className="symbol">USDT</span>
                <span className="balance">{currentCpt && Number(currentCpt.avatar)? currentCpt.avatarAssetList.USDT: 0}</span>
              </div>
            </ListItem>
            <ListItem>
              <img src={Fbtc20Icon} alt="" className='icon' />
              <div className="currency">
                <span className="symbol">F(BTC,20)</span>
                <span className="balance">{currentCpt && Number(currentCpt.avatar)? currentCpt.avatarAssetList[tokensSymbol.FBTC20]: 0}</span>
              </div>
            </ListItem>
     {/*       <ListItem>
              <Divider style={{width: '100%'}}/>
            </ListItem>
            <ListItem>
              <div className="currency" style={{marginLeft: 0, marginTop: -10}}>
                <span className="symbol">Hide Small Balances</span>
                <div style={{marginRight:' -9px'}}>
                  <SwitchTransparent  checked/>
                </div>
              </div>
            </ListItem>
         */} </List>
          <h3 className="daily">Daily Paid Rewards Reference</h3>
          <div className="daily_name">
            <span>{ Number(position)?  formatWBTC(positionInfoValues.estimatedDailyDebt) : 0} WBTC</span>
          </div>

          {
            pageType !== "Liq" && (<>
              <h3 className="daily">Mark Price</h3>
              <div className="daily_name">
                <span>1 F(BTC, 20)  {currentCpt && Number(currentCpt.avatar)? formatWBTC(BigNumber.from(currentCpt.tokensValues[currentCpt.token])) : 0} WBTC </span>
              </div> <Link to='/collateral' className="more">
              <span>Add More Collateral</span>&nbsp;&nbsp;<ArrowLine/>
            </Link>
            </>)
          }

        </AssetListWrap>
      </LeftWrap>
      <LeftWrap>
        <TitleContent>{pageType === 'Liq'? 'The Outstanding Debt': 'My Outstanding Debt'}</TitleContent>
        <AssetListWrap>
          <div className="ticker" style={{marginBottom: 0}}>
            <img src={Fbtc20Icon} alt="" className='icon'/>
            <div>
              <span>F(BTC, 20)</span>
              {/* @ts-ignore */}
              <span>{ balance?  balance / BigNumber.from(10).pow(decimals): 0 }</span>
            </div>
          </div>
          {
            pageType !== 'Liq' &&  <div className="more">
                  <a target="_blank" href={   `https://app.uniswap.org/#/swap?&outputCurrency=${address ? address[chainId] : ''}`
                  } rel="noreferrer">Buy More Asset to Neutralize</a>
              </div>
          }

        </AssetListWrap>
      </LeftWrap>
    </Wrapper>
  )
}
