import React, {useCallback, useContext, useState} from 'react';
import {Button, Stack} from '@mui/material';
import {styled} from '@mui/material/styles'
import {BigNumber} from "@ethersproject/bignumber";
import uuid from "uuid/dist/v4";
import  {SupportedChainId} from 'config/constants/chains'
import {buttonUnstyledClasses} from '@mui/base/ButtonUnstyled';
import {Button as ButtonStyled} from 'components/uikit/components/Button'
import {useWeb3React} from "web3-react-core";
import MinerABI from 'config/abi/Miner.json'
import addAsset from "../../../utils/addAsset";
import {getContract} from "../../../utils/web3React";
import {ComputedPowerTokenContext} from "../../../contexts/ComputedPowerTokenContext";
import useToast from "../../../hooks/useToast";
import {formatWBTC} from "../../../utils/formatTheValue";
import useSystemParams from "../../../hooks/useSystemParams";
import useHashPowerToken from "../../../hooks/useHashPowerToken";
import tokens from "../../../config/constants/tokens";
import contracts from "../../../config/constants/contracts";

const blue = {
  500: '#007FFF',
  600: '#0072E5',
  700: '#0059B2',
  color: '#2AA1FF'
};

const CustomButtonRoot = styled(Button)`
  //display: block;
  font-family: 'PingFang SC';
  font-size: 0.875rem;
  border-radius: 8px;
  transition: all 150ms ease;
  cursor: pointer;
  border: none;
  padding: 0;
  color: ${blue.color};
  text-align: left;

  &:hover {
    background-color: transparent;
  }

  &.${buttonUnstyledClasses.active} {
    background-color: transparent;
  }

  &.${buttonUnstyledClasses.focusVisible} {
    //box-shadow: 0 4px 20px 0 rgba(61, 71, 82, 0.1), 0 0 0 5px rgba(0, 127, 255, 0.5);
    outline: none;
  }

  &.${buttonUnstyledClasses.disabled} {
    opacity: 0.5;
    cursor: not-allowed;
  }
`;

const ActionButton = styled(Button)`
  text-transform: none;
  cursor: pointer;
  font-family: PingFangSC-Regular, sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 20px;
  color: #2AA1FF;
  padding: 0;

`
export const AlertButton = styled(ButtonStyled)`
  box-shadow: 0px 2px 0px rgba(0, 0, 0, 0.043);
  border-radius: 14px;
  background: linear-gradient(270deg, #7AE9F1 0%, #61C3F7 100%);
  border-radius: 14px;
  font-size: 14px;
  height: 32px;
  line-height: 32px;
  color: #000;
  font-weight: normal;
`

const { claimHelper } = contracts
// eslint-disable-next-line @typescript-eslint/no-unused-vars
function Action({row , address, rewardsData} ) {
  const { signer} = useSystemParams()
  const [claimStatus, setClaimStatus] = useState(false)
  const {setRefresh} = useHashPowerToken()
  const {connector, active, chainId, account} = useWeb3React()
  const {toastInfo, toastSuccess, toastError} = useToast()
  const {ticker} = row;
  const addToMetaMask = useCallback(async ()=>{
    if (active && connector && ticker) {
      addAsset({connector, tokenSymbol:ticker , chainId})
    }
  }, [connector, active, chainId, ticker]);


  const onClaim = useCallback(async ()=>{
    if (active && account && signer) {
      const miner = getContract(signer, MinerABI, row.address[chainId]);
      try {
        if (miner && Number(address) && row.address[chainId]) {
          let txInfo;
          // const ierc20 = getContract(signer, MinerABI, row.address[chainId]);
          // if (address === row.avatar) {
          //   const ierc20 = getContract(signer, MinerABI, row.address[chainId]);
          //   if (ierc20) {
          //    txInfo = await ierc20.transferFrom(row.address[chainId] , address, 0);
          //   }
          // }else{
          // const txInfo = await miner.claim(address);
          //   if (ierc20) {
          console.log(row.address[chainId], address)
          // eslint-disable-next-line prefer-const
             txInfo = await miner.transferFrom(row.address[chainId], address, 0);
            // }
          // const txInfo = await ierc20.transferFrom(row.address[chainId] , address, 0);
          // }

          if (txInfo) {
            let claimRewardsList = window.localStorage.getItem('claimRewardsList');
            if (claimRewardsList) {
              claimRewardsList = JSON.parse(claimRewardsList);
              window.localStorage.setItem('claimRewardsList', JSON.stringify([
                // @ts-ignore
                ...claimRewardsList,
                {
                  id: uuid(),
                  txHash: txInfo.hash,
                  date: new Date().getTime(),
                  tokenSymbol: row.token,
                  walletRewards: formatWBTC(BigNumber.from(rewardsData.walletRewards[0])),
                  avatarRewards: formatWBTC(BigNumber.from(rewardsData.avatarRewards[0])),
                  type: address === row.avatar? "Avatar" : 'My Wallet',
                }
              ]))
            }else{
              window.localStorage.setItem('claimRewardsList', JSON.stringify([
                {
                  id: uuid(),
                  txHash: txInfo.hash,
                  date: new Date().getTime(),
                  tokenSymbol: row.token,
                  walletRewards: formatWBTC(BigNumber.from(rewardsData.walletRewards[0])),
                  avatarRewards: formatWBTC(BigNumber.from(rewardsData.avatarRewards[0])),
                  type: address === row.avatar? "Avatar" : 'My Wallet',
                }
              ]))
            }

            toastInfo('Submitted')
            const result = await txInfo.wait();
            if (result) {
              setRefresh(state=> !state);
              toastSuccess('Success')
            }
          }

        }
      } catch (e) {
        toastError('Fail', e.message)
      }
    }
  }, [active, account, signer, row, chainId, address, rewardsData, toastInfo, setRefresh, toastSuccess, toastError])

  const claimAll = useCallback(async ()=>{
    if (signer) {
      const claimContract = getContract(signer, claimHelper.abi, claimHelper.address[chainId]);
      if (claimContract) {
        try {
          const params = [];
          // eslint-disable-next-line no-restricted-syntax
          // for (const item of hashPowerTokenList) {
            params.push([
              row.address[chainId],
              account,
            ])

          if (row.avatar && Number(row.avatar) > 0) {
            params.push([
              row.address[chainId],
              row.avatar,
            ])
          }
          setClaimStatus(true)
          // }
          const txInfo = await claimContract.multipleClaim(params);
          if (txInfo) {
            let claimRewardsList = window.localStorage.getItem('claimRewardsList');
            if (claimRewardsList) {
              claimRewardsList = JSON.parse(claimRewardsList);
              const _data = [
                // @ts-ignore
                ...claimRewardsList,
                {
                  id: uuid(),
                  txHash: txInfo.hash,
                  date: new Date().getTime(),
                  tokenSymbol: row.token,
                  walletRewards: formatWBTC(BigNumber.from(rewardsData.walletRewards[0])),
                  avatarRewards: formatWBTC(BigNumber.from(rewardsData.avatarRewards[0])),
                  type: 'My Wallet',
                }
              ]
              if (params.length > 1) {
                _data.push(
                  {
                    id: uuid(),
                    txHash: txInfo.hash,
                    date: new Date().getTime(),
                    tokenSymbol: row.token,
                    walletRewards: formatWBTC(BigNumber.from(rewardsData.walletRewards[0])),
                    avatarRewards: formatWBTC(BigNumber.from(rewardsData.avatarRewards[0])),
                    type: "Avatar",
                  })
              }
              window.localStorage.setItem('claimRewardsList', JSON.stringify(_data))
            }else{
              const _data = [
                {
                  id: uuid(),
                  txHash: txInfo.hash,
                  date: new Date().getTime(),
                  tokenSymbol: row.token,
                  walletRewards: formatWBTC(BigNumber.from(rewardsData.walletRewards[0])),
                  avatarRewards: formatWBTC(BigNumber.from(rewardsData.avatarRewards[0])),
                  type: 'My Wallet',
                }
              ]
              if (params.length>1) {
                _data.push(
                  {
                    id: uuid(),
                    txHash: txInfo.hash,
                    date: new Date().getTime(),
                    tokenSymbol: row.token,
                    walletRewards: formatWBTC(BigNumber.from(rewardsData.walletRewards[0])),
                    avatarRewards: formatWBTC(BigNumber.from(rewardsData.avatarRewards[0])),
                    type:"Avatar",
                  })
              }
              window.localStorage.setItem('claimRewardsList', JSON.stringify(_data))
            }



            toastInfo('Submitted');
            const result = await txInfo.wait();
            toastSuccess('Success');


            setRefresh(state=>!state)

            if (result) {
              setClaimStatus(false)
            }
          }
        } catch (e) {
          setClaimStatus(false)
          toastError('Fail', e.message)
        }
      }
    }
  }, [account, chainId, rewardsData, row, setRefresh, signer, toastError, toastInfo, toastSuccess])






  return (
    <div>

      <Stack direction="row" spacing={2}>
        <ActionButton disabled={claimStatus} onClick={claimAll}>Claim Rewards</ActionButton>
        <ActionButton onClick={()=>{
          window.open(`https://app.uniswap.org/#/swap?inputCurrency=${tokens.WBTC.address[chainId]}&outputCurrency=${row.address[chainId]}&chain=${SupportedChainId[chainId]}`)}}>Uniswap Trade</ActionButton>
        <ActionButton  onClick={addToMetaMask}>add to Metamask</ActionButton>
      </Stack>
    </div>
  );
}

export default Action
